<template>
  <div class="container-fluid" style="width: 60vw;">
    <h5 class="text-center">Прогноз ВРП с учетом инвестиций в строительство, млрд. тенге</h5>
        <div class="dropdown offset-11">
            <button style="font-size: .7rem" class="btn text-white btn-primary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                {{ htmlScenario }}
            </button>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                <a style="font-size: .7rem" @click="viewScenario(i)" v-for="i in scenaries" :key="i" class="dropdown-item">{{ i }}</a>
            </div>
        </div>
        <div class="row">
            <div class="col-12">
            <p style="text-align: center; font-size: .8rem"><b>Динамика валового регионального продукта, млрд. тенге</b></p>
                <BarChart
                :height="100"
                :chartData="vrpBar"
                :chartData2="vrpBar2"
                :chartLabels="years"
                :chartColors="regionsChartColors"
                label="ВРП, млрд. тг"
                label2="ВРП с учетом инвестиций в строительство, млрд. тг"
                />
            </div>
        </div>
        <div class="row">
            <div class="col-12">
                <p style="text-align: center; font-size: .8rem"><b>ИФО и дифлятор по отраслям, % к предыдущему году</b></p>
                <LineChart
                :height="100"
                :chartData="ifo"
                :chartData2="ifo2"
                :chartLabels="years"
                :chartColors="regionsChartColors"
                label="ИФО ВРП, %"
                label2="ИФО ВРП с учетом инвестиций в строительство, %"
                />
            </div>
        </div>
        <ul class="pagination pagination-sm d-flex justify-center">
            <v-card class="page-link" to="1">1</v-card>
            <v-card class="page-link disabled" to="2">2</v-card>
            <v-card class="page-link" to="3">3</v-card>
            <v-card class="page-link" to="4">4</v-card>
            <v-card class="page-link" to="5">5</v-card>
            <v-card class="page-link" to="6">6</v-card>
            <v-card class="page-link" to="7">7</v-card>
            <v-card class="page-link" to="8">8</v-card>
            <v-card class="page-link" to="9">9</v-card>
        </ul>
  </div>
</template>

<script>
import axios from "axios";

import LineChart from "./LineChart";

import BarChart from "./BarChart";

export default {
  components: {
    LineChart,
    BarChart,
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: ["#ff6b6b", "#ffe66d", "#ff6b6b", "#ffe66d"],
        pointHitRadius: 10
      },
      htmlScenario: 'Базовый',
      scenaries: ['Базовый', 'Оптимистичный', 'Пессимистичный'],
      years: [],
      vrpBar: [],
      vrpBar2: [],
      ifo: [],
      ifo2: []
    };
  },
  methods: {
    viewScenario(scenario) {
        this.htmlScenario = scenario
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: this.htmlScenario
            }
        }).then(arrRegions => {
            arrRegions.data.map(d => {
                if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП' && d["investments"] == 'Нет') {
                    this.years.push(d['year'])
                    this.vrpBar.push(d["value"].toFixed(1))
                }
                else if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП' && d["investments"] == 'Да') {
                    this.vrpBar2.push(d['value'].toFixed(1))
                }
                else if (d["indicator"] == 'ИФО ВРП, %' && d["industry"] == 'ВРП' && d["investments"] == 'Нет') {
                    this.ifo.push(d['value'].toFixed(1))
                }
                else if (d["indicator"] == 'ИФО ВРП, %' && d["industry"] == 'ВРП' && d["investments"] == 'Да') {
                    this.ifo2.push(d['value'].toFixed(1))
                }
            })
            this.years = this.years.splice(this.years.length - 7, this.years.length - 1)
            this.vrpBar = this.vrpBar.splice(this.vrpBar.length - 7, this.vrpBar.length - 1)
            this.vrpBar2 = this.vrpBar2.splice(this.vrpBar.length - 7, this.vrpBar.length - 1)
            this.ifo = this.ifo.splice(this.ifo.length - 7, this.ifo.length - 1)
            this.ifo2 = this.ifo2.splice(this.ifo2.length - 7, this.ifo2.length - 1)
        }).catch(err => console.log(err));
    },
  },
  created() {
    axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
        params: {
            scenario: this.htmlScenario
        }
    })
    .then((arrRegions) => {
        arrRegions.data.map(d => {
            if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП' && d["investments"] == 'Нет') {
                this.years.push(d['year'])
                this.vrpBar.push(d["value"].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП' && d["investments"] == 'Да') {
                this.vrpBar2.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ИФО ВРП, %' && d["industry"] == 'ВРП' && d["investments"] == 'Нет') {
                this.ifo.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ИФО ВРП, %' && d["industry"] == 'ВРП' && d["investments"] == 'Да') {
                this.ifo2.push(d['value'].toFixed(1))
            }
        })
        this.years = this.years.splice(this.years.length - 7, this.years.length - 1)
        this.vrpBar = this.vrpBar.splice(this.vrpBar.length - 7, this.vrpBar.length - 1)
        this.vrpBar2 = this.vrpBar2.splice(this.vrpBar.length - 7, this.vrpBar.length - 1)
        this.ifo = this.ifo.splice(this.ifo.length - 7, this.ifo.length - 1)
        this.ifo2 = this.ifo2.splice(this.ifo2.length - 7, this.ifo2.length - 1)
        // console.log(this.ifo2)
    })
    .catch(error => console.log(error))
  }
};
</script>

<style scoped>
    body{
        font-size: .7rem;
    }
    table {
        font-family: arial, sans-serif;
        border-collapse: collapse;
        width: 100%;
        font-size: .8rem;
    }

    td, th {
        border: 1px solid #ffd000;
        text-align: left;
        padding: 8px;
    }

    th {
        background-color: #ffd000;
    }

    tr:nth-child(even) {
        background-color: #dddddd;
    }

    .page-link {
        color: blue
    }

    .disabled {
        color: black
    }
</style>