<template>
  <div class="container-fluid" v-if="years.length > 0" style="width: 80vw">
        <h5 class="text-center">Таблица данных Валовой выпуск пчроизводства</h5>
    <div class="d-flex row">
            <div class="col-3">
                <button class="btn btn-primary m-1" style="font-size: .7rem" @click="allData">ВРП</button>
                <button class="btn btn-primary m-1" style="font-size: .7rem" @click="ifo">ИФО</button>
                <button class="btn btn-primary m-1" style="font-size: .7rem" @click="deflator">Дефлятор</button>
            </div>
            <div class="col-1 offset-7">
                <div class="dropdown text-white m-1">
                    <button style="font-size: .7rem; max-width: 200px; ovetflow: hidden" class="btn btn-primary filterName-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ currentRegion }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        <a style="font-size: .7rem;" @click="viewRegion(i)" v-for="i in regions" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
            </div>
            <div class="col-1">
                <div class="dropdown text-white m-1">
                    <button style="font-size: .7rem; max-width: 200px; ovetflow: hidden" class="btn btn-primary filterName-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ htmlScenario }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        <a style="font-size: .7rem;" @click="viewScenario(i)" v-for="i in scenaries" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
            </div>
    </div>
        <div class="row">
            <h6>{{ htmlIndicator }}</h6>
        </div>
        <div class="row" style="max-height: 80vh; overflow: hidden scroll;">
            <table class="table" v-if="tableAll">
                <svg v-if="myBtn" @click="tableProductionBtn()" class="link" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="30px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg>
                <tr>
                    <th>Направление</th>
                    <th v-for="year in years">{{ year }}</th>
                </tr>
                <tr>
                    <td>Производство товаров</td>
                    <td v-for="item in goods">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Промышленность</td>
                    <td v-for="item in industry">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Водоснабжение; канализационная система,<br> контроль над сбором и распределением отходов</td>
                    <td v-for="item in waterSupply">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Горнодобывающая промышленность и разработка карьеров</td>
                    <td v-for="item in mining">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Обрабатывающая промышленность</td>
                    <td v-for="item in manufacturing">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Электроснабжение, подача газа, пара и воздушное кондиционирование</td>
                    <td v-for="item in electricity">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Сельское, лесное и рыбное хозяйство</td>
                    <td v-for="item in agriculture">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Строительство</td>
                    <td v-for="item in building">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Производство услуг</td>
                    <td v-for="item in service">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Оптовая торговля</td>
                    <td v-for="item in wholesale">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Розничная торговля</td>
                    <td v-for="item in retail">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Всего</td>
                    <td v-for="item in total">{{ item.toFixed(1) }}</td>
                </tr>
            </table>
            <table class="table" v-if="tableProduction">
                <button @click="tableAllBtn()" class="link">Всё</button>
                <tr>
                    <th>Направление</th>
                    <th v-for="year in years" :key="year">{{ year }}</th>
                </tr>
                <tr>
                    <td class="link" @click="tableGoodsBtn()">Производство товаров</td>
                    <td v-for="item in goods" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td class="link" @click="tableServicesBtn()">Производство услуг</td>
                    <td v-for="item in service" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
            </table>
            <table class="table" v-if="tableGoods">
                <svg @click="tableProductionBtn()" class="link" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="30px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg>
                <button @click="tableAllBtn()" class="link">Всё</button>
                <tr>
                    <th>Направление</th>
                    <th v-for="year in years" :key="year">{{ year }}</th>
                </tr>
                <tr>
                    <td class="link" @click="tableIndustriesBtn()">Промышленность</td>
                    <td v-for="item in industry" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Сельское, лесное и рыбное хозяйство</td>
                    <td v-for="item in agriculture" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Строительство</td>
                    <td v-for="item in building" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
            </table>
            <table class="table" v-if="tableServices">
                <svg @click="tableProductionBtn()" class="link" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="30px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg>
                <button @click="tableAllBtn()" class="link">Всё</button>
                <tr>
                    <th>Направление</th>
                    <th v-for="year in years" :key="year">{{ year }}</th>
                </tr>
                <tr>
                    <td>Производство услуг</td>
                    <td v-for="item in service" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Информация и связь</td>
                    <td v-for="item in other" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Операции с недвижимым имуществом</td>
                    <td v-for="item in other" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Прочие услуги</td>
                    <td v-for="item in other" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Оптовая торговля</td>
                    <td v-for="item in wholesale" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Розничная торговля</td>
                    <td v-for="item in retail" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Транспорт и складирование</td>
                    <td v-for="item in other" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
            </table>
            <table class="table" v-if="tableIndustries">
                <svg @click="tableGoodsBtn()" class="link" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="30px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg>
                <button @click="tableAllBtn()" class="link">Всё</button>
                <tr>
                    <th>Направление</th>
                    <th v-for="year in years" :key="year">{{ year }}</th>
                </tr>
                <tr>
                    <td>Промышленность</td>
                    <td v-for="item in industry" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Водоснабжение; канализационная система,<br> контроль над сбором и распределением отходов</td>
                    <td v-for="item in waterSupply" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Горнодобывающая промышленность и разработка карьеров</td>
                    <td v-for="item in mining" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Обрабатывающая промышленность</td>
                    <td v-for="item in manufacturing" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
                <tr>
                    <td>Электроснабжение, подача газа, пара и воздушное кондиционирование</td>
                    <td v-for="item in electricity" :key="item">{{ item.toFixed(1) }}</td>
                </tr>
            </table>
        </div>
        <ul class="pagination pagination-sm d-flex justify-center">
            <v-card class="page-link" to="1">1</v-card>
            <v-card class="page-link" to="2">2</v-card>
            <v-card class="page-link" to="3">3</v-card>
            <v-card class="page-link" to="4">4</v-card>
            <v-card class="page-link" to="5">5</v-card>
            <v-card class="page-link" to="6">6</v-card>
            <v-card class="page-link" to="7">7</v-card>
            <v-card class="page-link" to="8">8</v-card>
            <v-card class="page-link disabled" to="9">9</v-card>
        </ul>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      yearsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: "orange",
        pointHitRadius: 10
      },
      htmlScenario: 'Базовый',
      scenaries: ['Базовый', 'Оптимистичный', 'Пессимистичный'],
      myIndicator: 'Выпуск продукции, млн. тенге',
      htmlIndicator: 'Валовой выпуск продукции, млрд. тенге',
      currentRegion: 'Атырау',
      regions: [],
      years: [],
      goods: [],
      industry: [],
      waterSupply: [],
      mining: [],
      manufacturing: [],
      electricity: [],
      agriculture: [],
      building: [],
      service: [],
      wholesale: [],
      retail: [],
      total: [],
      other: [0],
      tableAll: true,
      tableProduction: false,
      tableGoods: false,
      tableServices: false,
      tableIndustries: false,
      myBtn: true
    };
  },
  methods: {
    viewRegion(region){
        this.currentRegion = region
        this.goods = []
        this.industry = []
        this.waterSupply = []
        this.mining = []
        this.manufacturing = []
        this.electricity = []
        this.agriculture = []
        this.building = []
        this.service = []
        this.wholesale = []
        this.retail = []
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpraionscenario', {
        params: {
            scenario: this.htmlScenario
            }
        })
        .then((arryears) => {
            arryears.data.map(d => {
            if (d['indicator'] == this.myIndicator && d['industry'] == 'Производство товаров' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.goods.push(d['value'] / 1000)
                }else {
                    this.goods.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Промышленность' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.industry.push(d['value'] / 1000)
                }else {
                    this.industry.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Водоснабжение; канализационная система, контроль над сбором и распределением отходов' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.waterSupply.push(d['value'] / 1000)
                }else {
                    this.waterSupply.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Горнодобывающая промышленность и разработка карьеров' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.mining.push(d['value'] / 1000)
                }else {
                    this.mining.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Обрабатывающая промышленность' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.manufacturing.push(d['value'] / 1000)
                }else {
                    this.manufacturing.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Электроснабжение, подача газа, пара и воздушное кондиционирование' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.electricity.push(d['value'] / 1000)
                }else {
                    this.electricity.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Сельское, лесное и рыбное хозяйство' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.agriculture.push(d['value'] / 1000)
                }else {
                    this.agriculture.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Строительство' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.building.push(d['value'] / 1000)
                }else {
                    this.building.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Производство услуг' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.service.push(d['value'] / 1000)
                }else {
                    this.service.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Оптовая торговля' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.wholesale.push(d['value'] / 1000)
                }else {
                    this.wholesale.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Розничная торговля' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.retail.push(d['value'] / 1000)
                }else {
                    this.retail.push(d['value'])
                }
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Всего' && d['region'] == this.currentRegion) {
                if(this.myIndicator == 'Выпуск продукции, млн. тенге'){
                    this.total.push(d['value'] / 1000)
                }else {
                    this.total.push(d['value'])
                }
            }
        })
        this.goods = this.goods.splice(this.goods.length - 15, this.goods.length - 1)
        this.industry = this.industry.splice(this.industry.length - 15, this.industry.length - 1)
        this.waterSupply = this.waterSupply.splice(this.waterSupply.length - 15, this.waterSupply.length - 1)
        this.mining = this.mining.splice(this.mining.length - 15, this.mining.length - 1)
        this.manufacturing = this.manufacturing.splice(this.manufacturing.length - 15, this.manufacturing.length - 1)
        this.electricity = this.electricity.splice(this.electricity.length - 15, this.electricity.length - 1)
        this.agriculture = this.agriculture.splice(this.agriculture.length - 15, this.agriculture.length - 1)
        this.building = this.building.splice(this.building.length - 15, this.building.length - 1)
        this.service = this.service.splice(this.service.length - 15, this.service.length - 1)
        this.wholesale = this.wholesale.splice(this.wholesale.length - 15, this.wholesale.length - 1)
        this.retail = this.retail.splice(this.retail.length - 15, this.retail.length - 1)
        this.total = this.total.splice(this.total.length - 15, this.total.length - 1)
        }).catch(error => console.log(error))
    },
    ifo(){
        this.myBtn = false
        this.tableAll = true
        this.tableProduction = false
        this.tableGoods = false
        this.tableServices = false
        this.tableIndustries = false
        this.myIndicator = 'ИФО ВВ, %'
        this.htmlIndicator = 'ИФО валового выпуска, % к предыдущему году'
        this.goods = []
        this.industry = []
        this.waterSupply = []
        this.mining = []
        this.manufacturing = []
        this.electricity = []
        this.agriculture = []
        this.building = []
        this.service = []
        this.wholesale = []
        this.retail = []
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpraionscenario', {
        params: {
            scenario: this.htmlScenario
            }
        })
        .then((arryears) => {
            arryears.data.map(d => {
            if (d['indicator'] == this.myIndicator && d['industry'] == 'Производство товаров' && d['region'] == this.currentRegion) {
                this.goods.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Промышленность' && d['region'] == this.currentRegion) {
                this.industry.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Водоснабжение; канализационная система, контроль над сбором и распределением отходов' && d['region'] == this.currentRegion) {
                this.waterSupply.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Горнодобывающая промышленность и разработка карьеров' && d['region'] == this.currentRegion) {
                this.mining.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Обрабатывающая промышленность' && d['region'] == this.currentRegion) {
                this.manufacturing.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Электроснабжение, подача газа, пара и воздушное кондиционирование' && d['region'] == this.currentRegion) {
                this.electricity.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Сельское, лесное и рыбное хозяйство' && d['region'] == this.currentRegion) {
                this.agriculture.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Строительство' && d['region'] == this.currentRegion) {
                this.building.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Производство услуг' && d['region'] == this.currentRegion) {
                this.service.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Оптовая торговля' && d['region'] == this.currentRegion) {
                this.wholesale.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Розничная торговля' && d['region'] == this.currentRegion) {
                this.retail.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == 'Всего' && d['region'] == this.currentRegion) {
                this.total.push(d['value'])
            }
        })
        this.goods = this.goods.splice(this.goods.length - 15, this.goods.length - 1)
        this.industry = this.industry.splice(this.industry.length - 15, this.industry.length - 1)
        this.waterSupply = this.waterSupply.splice(this.waterSupply.length - 15, this.waterSupply.length - 1)
        this.mining = this.mining.splice(this.mining.length - 15, this.mining.length - 1)
        this.manufacturing = this.manufacturing.splice(this.manufacturing.length - 15, this.manufacturing.length - 1)
        this.electricity = this.electricity.splice(this.electricity.length - 15, this.electricity.length - 1)
        this.agriculture = this.agriculture.splice(this.agriculture.length - 15, this.agriculture.length - 1)
        this.building = this.building.splice(this.building.length - 15, this.building.length - 1)
        this.service = this.service.splice(this.service.length - 15, this.service.length - 1)
        this.wholesale = this.wholesale.splice(this.wholesale.length - 15, this.wholesale.length - 1)
        this.retail = this.retail.splice(this.retail.length - 15, this.retail.length - 1)
        this.total = this.total.splice(this.total.length - 15, this.total.length - 1)
        }).catch(error => console.log(error))
   },
    deflator(){
        this.myBtn = false
        this.tableAll = true
        this.tableProduction = false
        this.tableGoods = false
        this.tableServices = false
        this.tableIndustries = false
        this.myIndicator = 'Дефлятор ВВ, %'
        this.htmlIndicator = 'Дефлятор Валового выпуска, % к предыдущему году'
        this.goods = []
        this.industry = []
        this.waterSupply = []
        this.mining = []
        this.manufacturing = []
        this.electricity = []
        this.agriculture = []
        this.building = []
        this.service = []
