<template>
  <div class="container-fluid" style="width: 60vw;" v-if="allData">
      <div class="d-flex">
        <v-card style="width: 0px" to="/main/social-eco-dev"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="20px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg></v-card>
        <h3 class="mx-auto">Молодежная политика</h3>
      </div>
    <div class="row d-flex">
            <div class="dropdown col-5" v-if="MQIndicator">
                <p>Показатель</p>
                <button style="font-size: .7rem; max-width: 20vw; overflow-x: hidden" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" >
                    {{ htmlIndex }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow-x: hidden;">
                    <a style="font-size: .7rem" @click="changeIndicator(i)" v-for="i in Object.keys(indicators)" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown col-5" v-if="yearsIndicator">
                <p>Показатель</p>
                <button style="font-size: .7rem; max-width: 20vw; overflow-x: hidden" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" >
                    {{ htmlIndex }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow-x: hidden;">
                    <a style="font-size: .7rem" @click="changeIndicatorToYears(i)" v-for="i in Object.keys(indicators)" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <button class="btn btn-primary col-2" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeQuarters()">Ежеквартальные</button>
            <button class="btn btn-primary col-2" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeHalfs()">Полугодие</button>
            <button class="btn btn-primary col-1" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeYears()"
            >Годовые</button>
            <div class="dropdown col-1" v-if="yearsBtn1">
                <p>Год</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="changeYearByQuarters(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown col-1" v-if="yearsBtn2">
                <p>Год</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="changeYearByHalfs(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
    </div>
        <div class="row">
            <div class="col-12" v-if="MQChart">
                <HorizontalBar
                :height="200"
                :chartData="Object.values(changeData)"
                :chartLabels="Object.keys(changeData)"
                :chartColors="regionsChartColors"
                :label="htmlIndex"
                />
            </div>
            <div class="col-12" v-if="yearsChart">
                <HorizontalBar
                :height="200"
                :chartData="changeData"
                :chartLabels="byYearsFromYears"
                :chartColors="regionsChartColors"
                :label="htmlIndex"
                />
            </div>
        </div>
  </div>
</template>

<script>
import axios from "axios";

import HorizontalBar from "./HorizontalBar";

export default {
  components: {
    HorizontalBar
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: "rgb(98, 153, 235)",
        pointHitRadius: 10
      },
      htmlYears: '2019',
      htmlIndex: 'Уровень безработной молодежи, %',
      regions: [],
      years: [],
      allData: [],
      changeData: [],
      indicators: {},
      indicatorsByQuarters: {'Уровень безработной молодежи, %':'youthUnemploymentRate'},

      indicatorsByHalfs: {'Количество социальных проектов в сфере государственной молодежной политики, единиц':'numberSocialProjects', 'Количество привлеченной молодежи в трудовые, строительные и педагогические отряды, человек':'numberYoungpeopleInvolvedTeams', 'Количество молодежи привлеченной к работе молодежных общественных объединений, единиц':'numberYoungpeopleInvolvedWork', 'Количество молодежи трудоустроенной в рамках программы "Молодежная практика", человек':'youthPractice', 'Охват молодежи по программе "Жас Отау", человек':'jasOtau', 'Охват молодых семей жильём в рамках пограммы "Доступное жильё", человек':'affordableHousing', 'Количество молодежи трудоустроенной в рамках программы "С дипломом в село", единиц':'withDiplomaVillage', 'Доля молодежных общественных объединений, участвующих в государственном социальном заказе, %':'shareYouthSocialOrder', 'Охват молодежи мероприятиями в сфере молодежной политики, %':'youthOutreachYouthPolicy'},

      indicatorsByYears: {'Охват молодежи мероприятиями в сфере молодежной политики, человек':'youthOutreachActivities', 'Количество молодёжных общественных объединений в регионе, единиц':'youthPublicAssociationsRegion', 'Количество молодежи, привлеченной к работе молодежных общественных объединений, человек':'youthInvolvedWork', 'Численность молодежи области, человек':'numberYoungpeopleRegion', 'Женщины, человек':'women', 'Мужчины, человек':'men', 'Сельская и городская молодежь, человек':'ruralUrbanYouth', 'Городская молодежь, человек':'urbanYouth', 'Сельская молодежь, человек':'ruralYouth', 'Сельская молодежь, %':'ruralYouthpercent', 'Городская молодежь, %':'urbanYouthPercent'},
      yearsBtn1: true,
      yearsBtn2: false,
      yearsIndicator: false,
      MQIndicator: true,
      MQChart: true,
      yearsChart: false,
      byYearsFromYears: [],
      test: {}
    };
  },
  methods: {
    updateStatistics(update, index) {
        // this.allData = update.data
        let allDatas = update.data
        let key = index[this.htmlIndex]
        this.changeData = allDatas[key]
    },
    changeYears() {
        this.yearsBtn1 = false
        this.yearsBtn2 = false
        this.MQIndicator = false
        this.MQChart = false
        this.yearsIndicator = true
        this.yearsChart = true
        this.htmlIndex = 'Охват молодежи мероприятиями в сфере молодежной политики, человек'
        this.indicators = this.indicatorsByYears

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/molodejnaya', {
            params: {
                byYears: true
            }
        })
        .then(arrMonths => {
            this.allData = arrMonths.data
            this.byYearsFromYears = []
            arrMonths.data.map(n => this.byYearsFromYears.push(n.year))
            let key = this.indicators[this.htmlIndex]
            this.changeData = arrMonths.data.map(d=>d[key])
            // this.updateStatistics(arrMonths, this.indicators)
        })
        .catch(error => console.log(error))
    },
    changeHalfs() {
        this.yearsBtn1 = false
        this.yearsBtn2 = true
        this.MQIndicator = true
        this.MQChart = true
        this.yearsIndicator = false
        this.yearsChart = false
        this.htmlIndex = 'Количество социальных проектов в сфере государственной молодежной политики, единиц'
        this.indicators = this.indicatorsByHalfs

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/molodejhy', {
            params: {
                year: this.htmlYears,
                byHalfs: true
            }
        })
        .then(arrMonths => {
            this.allData = arrMonths.data
            this.updateStatistics(arrMonths, this.indicators)
        })
        .catch(error => console.log(error))
    },
    changeQuarters() {
        this.yearsBtn1 = true
        this.yearsBtn2 = false
        this.MQIndicator = true
        this.MQChart = true
        this.yearsIndicator = false
        this.yearsChart = false
        this.htmlIndex = 'Уровень безработной молодежи, %'
        this.indicators = this.indicatorsByQuarters

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/molodejq', {
            params: {
                year: this.htmlYears,
                byQuarters: true
            }
        })
        .then(arrMonths => {
            this.allData = arrMonths.data
            this.updateStatistics(arrMonths, this.indicators)
        })
        .catch(error => console.log(error))
    },
    changeIndicator(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData[key]
    },
    changeIndicatorToYears(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData.map(d=>d[key])
    },
    changeYearByQuarters(year) {
        this.htmlYears = year
        this.indicators = this.indicatorsByQuarters
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/molodejq', {
            params: {
                year: year,
                byQuarters: true
            }
        }).then(d => {
            this.updateStatistics(d, this.indicators)
        }).catch(err => console.log(err));
    },
    changeYearByHalfs(year) {
        this.htmlYears = year
        this.indicators = this.indicatorsByHalfs
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/molodejhy', {
            params: {
                year: year,
                byHalfs: true
            }
        }).then(d => {
            this.updateStatistics(d, this.indicators)
        }).catch(err => console.log(err));
    }
  },
  created() {
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/molodejnaya', {
            params: {
                byYears: true
            }
        })
        .then(arrYears => {
            arrYears.data.map(d => {
                this.years.push(d['year'])
            })
            this.years = this.years.sort()
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/molodejq', {
            params: {
                year: this.htmlYears,
                byQuarters: true
            }
        })
        .then(arrMonths => {            
            this.indicators = this.indicatorsByQuarters
            this.allData = arrMonths.data
            this.updateStatistics(arrMonths, this.indicators)
        })
        .catch(error => console.log(error))
  }
};
</script>

<style scoped>
    .nav-link{
        font-size: .7rem;
    }
</style>