<template>
  <div class="container-fluid" v-if="y2k12">
        <h5 class="text-center">Валовый региональный продукт, млрд. тенге</h5>
            <div class="d-flex">
                <div class="dropdown col-2">
                    <button style="font-size: .7rem" class="btn btn-success text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ scenarioHtml }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        <a style="font-size: .7rem" @click="viewScenario(i)" v-for="i in scenario" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
            </div>
        <div class="row">
            <div class="col-12">
            <p style="text-align: center; font-size: .8rem"><b>Динамика валового регионального продукта, млрд. тенге</b></p>
                <mixedChart
                :height="55"
                :chartData="[y2011, y2012, y2013, y2014, y2015, y2016, y2017, y2018, y2019, y2020, y2021, y2022, y2023, y2024, y2025]"
                :chartData2="ifoLine"
                :chartLabels="years"
                :chartColors="regionsChartColors"
                label="Валовый выпуск, млрд. тг"
                label2="ИФО ВВ, %"
                />
            </div>
        </div>
        <div class="row">
            <div class="col-12">
                <div class="dropdown col-1">
                    <button style="font-size: .7rem" class="btn btn-success text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ htmlYears }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow: hidden scroll">
                        <a style="font-size: .7rem;" @click="viewYear(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
                <div class="col-12">
                <p style="text-align: center; font-size: .8rem"><b>ИФО и дефлятор по отраслям, % к предыдущему году</b></p>
                    <BarChart
                    :height="55"
                    :chartData="ifo"
                    :chartData2="deflator"
                    :chartLabels="industry"
                    :chartColors="regionsChartColors"
                    :options="barOptions"
                    label="ИФО ВВ, %(отрасли)"
                    label2="Дефлятор ВВ, %(отрасли)"
                    />
                </div>
            </div>
        </div>
        <p style="text-align: center; font-size: .8rem"><b>ВРП по отраслям, млрд. тенге</b></p>
        <div class="col-12 d-flex justify-center">
            <div class="col-6" v-if="pieGoods" @click="pieFIlter()">
                <p class="text-center" style="font-size: .8rem">Производство товаров</p>
                <PieChart
                :height="200"
                :chartData="[household, pieIndustry, building]"
                :chartLabels="['Сельское, лесное и рыбное хозяйство', 'Промышленность', 'Строительство']"
                :chartColors="regionsChartColors"
                :options="pieOptions"
                />
            </div>
            <div class="col-6" v-if="pieIndustries">
                <p class="text-center" style="font-size: .8rem">Промышленность</p>
                <svg @click="backBtn()" class="link" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="30px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg>
                <PieChart
                :height="200"
                :chartData="[mining, manufacturing, powerSupply, waterSupply]"
                :chartLabels="['Горнодобывающая промышленность', 'Обрабатывающая промышленность', 'Электроснабжение', 'Водоснабжение']"
                :chartColors="regionsChartColors"
                :options="pieOptions"
                />
            </div>
            <div class="col-6">
                <p class="text-center" style="font-size: .8rem">Производство услуг</p>
                <PieChart
                :height="200"
                :chartData="[trade, transport, estate, other, communication]"
                :chartLabels="['Торговля', 'Транспорт и складирование', 'Операции с недвижимым имуществом', 'Прочие услуги', 'Информация и связь']"
                :chartColors="regionsChartColors"
                :options="pieOptions"
                />
            </div>
        </div>
        <ul class="pagination pagination-sm d-flex justify-center mt-2">
            <v-card class="page-link" to="1">1</v-card>
            <v-card class="page-link" to="2">2</v-card>
            <v-card class="page-link disabled" to="3">3</v-card>
            <v-card class="page-link" to="4">4</v-card>
            <v-card class="page-link" to="5">5</v-card>
            <v-card class="page-link" to="6">6</v-card>
            <v-card class="page-link" to="7">7</v-card>
            <v-card class="page-link" to="8">8</v-card>
            <v-card class="page-link" to="9">9</v-card>
        </ul>
  </div>
</template>

<script>
import axios from "axios";

import BarChart from "./BarChart";

import mixedChart from "./mixedChart";

import PieChart from "./PieChart";

export default {
  components: {
    BarChart,
    mixedChart,
    PieChart
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: '#ffe66d',
        pointHitRadius: 10,
        options: {
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: false,
                            // max: 30000,
                        }
                    }]
                },
                legend: {
                    align: 'start'
                }
            },
      },
        pieOptions: {
                legend: {
                    position: 'top',
                    align: 'center'
                }
            },
      barOptions: {
            scales: {
                yAxes: [{
                    ticks: {
                        beginAtZero: true,
                    }
                }]
            }
        },
      htmlYears: '2019',
      years: [],
      scenarioHtml: 'Базовый',
      scenario: ['Базовый', 'Оптимистичный', 'Пессимистичный'],
      industry: [],
      pieIndustry: [],
      indicator: [],
      ifo: [],
      deflator: [],
      production: [],
      production2: [],
      household: [],
      building: [],
      mining: [],
      manufacturing: [],
      powerSupply: [],
      waterSupply: [],
      trade: [],
      rozTrade: [],
      optTrade: [],
      transport: [],
      estate: [],
      other: [],
      communication: [],
      ifoLine: [],
      y2011: [],
      y2012: [],
      y2013: [],
      y2014: [],
      y2015: [],
      y2016: [],
      y2017: [],
      y2018: [],
      y2019: [],
      y2020: [],
      y2021: [],
      y2022: [],
      y2023: [],
      y2024: [],
      y2025: [],
      //////////
      y2k11: [],
      y2k12: [],
      y2k13: [],
      y2k14: [],
      y2k15: [],
      y2k16: [],
      y2k17: [],
      y2k18: [],
      y2k19: [],
      y2k20: [],
      y2k21: [],
      y2k22: [],
      y2k23: [],
      y2k24: [],
      y2k25: [],
      pieGoods: true,
      pieIndustries: false
    };
  },
  methods: {
    pieFIlter() {
        this.pieGoods = false
        this.pieIndustries = true
    },
    backBtn() {
        this.pieGoods = true
        this.pieIndustries = false
    },
    viewYear(year) {
        this.htmlYears = year
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblastyear', {
            params: {
                year: this.htmlYears
            }
        }).then(arrRegions => {
            this.industry = []
            this.ifo = []
            this.deflator = []
            this.production = []
            this.production2 = []
            this.household = []
            this.building = []
            this.mining = []
            this.pieIndustry = []
            this.manufacturing = []
            this.powerSupply = []
            this.waterSupply = []
            this.transport = []
            this.rozTrade = []
            this.optTrade = []
            this.estate = []
            this.other = []
            this.communication = []
            this.trade = []

            arrRegions.data.map(d => {
            if (d["indicator"] == 'ИФО ВВ, %' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' && d["industry"] !== 'Всего') {
                if (d["industry"] !== 'Производство товаров') {
                    // if (d["industry"] !== 'Промышленность') {
                        // if (d["industry"] !== 'Производство услуг') {
                            if (d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов') {
                                if (d["industry"] !== 'Розничная торговля') {
                                    if (d["industry"] !== 'Оптовая торговля') {
                                        this.ifo.push(d['value'].toFixed(1))
                                        this.industry.push(d['industry'])
                                    }
                                }
                            }
                        // }
                    // }
                }
            }
            else if (d["indicator"] == 'Дефлятор ВВ, %' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' && d["industry"] !== 'Всего') {
                if (d["industry"] !== 'Производство товаров') {
                    if (d["industry"] !== 'Промышленность') {
                        if (d["industry"] !== 'Производство услуг') {
                            // if (d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов') {
                                if (d["industry"] !== 'Розничная торговля') {
                                    if (d["industry"] !== 'Оптовая торговля') {
                                        this.deflator.push(d['value'].toFixed(1))
                                    }
                                }
                            // }
                        }
                    }
                }
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Производство товаров') {
                this.production.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Производство услуг') {
                this.production2.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Сельское, лесное и рыбное хозяйство') {
                this.household.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Промышленность') {
                this.pieIndustry.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Строительство') {
                this.building.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Горнодобывающая промышленность и разработка карьеров') {
                this.mining.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Обрабатывающая промышленность') {
                this.manufacturing.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Электроснабжение, подача газа, пара и воздушное кондиционирование') {
                this.powerSupply.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Водоснабжение; канализационная система, контроль над сбором и распределением отходов') {
                this.waterSupply.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Оптовая торговля') {
                this.optTrade.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Розничная торговля') {
                this.rozTrade.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Транспорт и складирование') {
                this.transport.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Операции с недвижимым имуществом') {
                this.estate.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Прочие услуги') {
                this.other.push(d['value'])
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет' 
            && d["industry"] == 'Информация и связь') {
                this.communication.push(d['value'])
            }
        })
        this.production = (this.production / 1000).toFixed(2)
        this.production2 = (this.production2 / 1000).toFixed(2)
        this.household = (this.household / 1000).toFixed(2)
        this.pieIndustry = (this.pieIndustry / 1000).toFixed(2)
        this.building = (this.building / 1000).toFixed(2)
        this.mining = (this.mining / 1000).toFixed(2)
        this.manufacturing = (this.manufacturing / 1000).toFixed(2)
        this.powerSupply = (this.powerSupply / 1000).toFixed(2)
        this.waterSupply = (this.waterSupply / 1000).toFixed(2)
        this.transport = (this.transport / 1000).toFixed(2)
        this.rozTrade = (this.rozTrade / 1000).toFixed(2)
        this.optTrade = (this.optTrade / 1000).toFixed(2)
        this.estate = (this.estate / 1000).toFixed(2)
        this.other = (this.other / 1000).toFixed(2)
        this.communication = (this.communication / 1000).toFixed(2)
        this.trade = parseFloat(this.rozTrade) + parseFloat(this.optTrade)
        }).catch(err => console.log(err));
    },
    viewScenario(scenario) {
        this.scenarioHtml = scenario
        this.y2011 = []
        this.y2012 = []
        this.y2013 = []
        this.y2014 = []
        this.y2015 = []
        this.y2016 = []
        this.y2017 = []
        this.y2018 = []
        this.y2019 = []
        this.y2020 = []
        this.y2021 = []
        this.y2022 = []
        this.y2023 = []
        this.y2024 = []
        this.y2025 = []
        this.y2k11 = []
        this.y2k12 = []
        this.y2k13 = []
        this.y2k14 = []
        this.y2k15 = []
        this.y2k16 = []
        this.y2k17 = []
        this.y2k18 = []
        this.y2k19 = []
        this.y2k20 = []
        this.y2k21 = []
        this.y2k22 = []
        this.y2k23 = []
        this.y2k24 = []
        this.y2k25 = []
        this.ifoLine = []

        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: this.scenarioHtml
            }
        }).then(arrRegions => {
        // 2011
        arrRegions.data.map(d => {
            if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Сельское, лесное и рыбное хозяйство') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Горнодобывающая промышленность и разработка карьеров') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Обрабатывающая промышленность') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Электроснабжение, подача газа, пара и воздушное кондиционирование') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Водоснабжение; канализационная система, контроль над сбором и распределением отходов') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Строительство') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Розничная торговля') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Оптовая торговля') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Транспорт и складирование') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Информация и связь') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Операции с недвижимым имуществом') {
                this.y2k11.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2011'
                && d["industry"] == 'Прочие услуги') {
                this.y2k11.push(d['value'] / 1000)
            }
        })
        this.y2011 = (this.y2k11[0] + this.y2k11[1] + this.y2k11[2] + this.y2k11[3] + this.y2k11[4] + this.y2k11[5] + this.y2k11[6] + this.y2k11[7] + this.y2k11[8] + this.y2k11[9] + this.y2k11[10] + this.y2k11[11] + this.y2k11[12]).toFixed(2)
        console.log(this.y2k11)
        // 2012
        arrRegions.data.map(d => {
            if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2012'
                && d["industry"] == 'Сельское, лесное и рыбное хозяйство') {
                this.y2k12.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2012'
                && d["industry"] == 'Горнодобывающая промышленность и разработка карьеров') {
                this.y2k12.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2012'
                && d["industry"] == 'Обрабатывающая промышленность') {
                this.y2k12.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2012'
                && d["industry"] == 'Электроснабжение, подача газа, пара и воздушное кондиционирование') {
                this.y2k12.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2012'
                && d["industry"] == 'Водоснабжение; канализационная система, контроль над сбором и распределением отходов') {
                this.y2k12.push(d['value'] / 1000)
            }
            else if (d["indicator"] == 'Выпуск продукции, млн. тенге' && d["investments"] == 'Нет' && d["year"] == '2012'
                && d["industry"] == 'Строительство') {
