<template>
  <div class="container-fluid" style="width: 60vw;" v-if="years">
        <h5 class="text-center">Сравнение показателей по сценариям</h5>
        <div class="d-flex">
            <div class="col-8">
                <button class="btn btn-primary m-1" style="font-size: .7rem" @click="allData">ВРП</button>
                <button class="btn btn-primary m-1" style="font-size: .7rem" @click="ifo">ИФО</button>
                <button class="btn btn-primary m-1" style="font-size: .7rem" @click="deflator">Дефлятор</button>
            </div>
            <div class="col-5 d-flex">
                <button class="btn btn-primary m-1" style="font-size: .7rem" @click="allData">Всего</button>
                <div class="dropdown text-white m-1">
                    <button style="font-size: .7rem; max-width: 200px; ovetflow: hidden" :title="filterName" class="btn btn-primary filterName-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ filterName }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow: hidden scroll">
                        <a style="font-size: .7rem;" @click="industryFilter(i, myIndicator)" v-for="i in industries" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
            </div>
        </div>
        <h5 style="font-size: .9rem">{{ htmlMain }}</h5>
            <div class="row">
                <div class="col-12" v-if="basic">
                    <HorizontalBar
                    :height="70"
                    :chartData="basic"
                    :chartLabels="years"
                    :chartColors="regionsChartColors"
                    :currentColor="colorBlue"
                    label="Сценарий: базовый"
                    />
                </div>
            </div>
            <div class="row">
                <div class="col-12" v-if="optimistic">
                    <HorizontalBar
                    :height="70"
                    :chartData="optimistic"
                    :chartLabels="years"
                    :chartColors="regionsChartColors"
                    :currentColor="colorYellow"
                    label="Сценарий: оптимистичный"
                    />
                </div>
            </div>
            <div class="row">
                <div class="col-12" v-if="pessimistic">
                    <HorizontalBar
                    :height="70"
                    :chartData="pessimistic"
                    :chartLabels="years"
                    :chartColors="regionsChartColors"
                    :currentColor="colorRed"
                    label="Сценарий: пессимистичный"
                    />
                </div>
            </div>
        <ul class="pagination pagination-sm d-flex justify-center">
            <v-card class="page-link" to="1">1</v-card>
            <v-card class="page-link" to="2">2</v-card>
            <v-card class="page-link" to="3">3</v-card>
            <v-card class="page-link" to="4">4</v-card>
            <v-card class="page-link" to="5">5</v-card>
            <v-card class="page-link disabled" to="6">6</v-card>
            <v-card class="page-link" to="7">7</v-card>
            <v-card class="page-link" to="8">8</v-card>
            <v-card class="page-link" to="9">9</v-card>
        </ul>
  </div>
</template>

<script>
import axios from "axios";

import HorizontalBar from "./HorizontalBar";

export default {
  components: {
    HorizontalBar
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: ["orange", "red", "blue", "green", "tomato", "aqua"],
        pointHitRadius: 10,
        options: {
            legend: {
                align: 'start'
            }
        }
      },
      filterName: 'По отраслям',
      scenarioHtml: 'Базовый',
      scenario: ['Базовый', 'Оптимистичный', 'Пессимистичный'],
      years: [],
      basic: [],
      optimistic: [],
      pessimistic: [],
      industries: [],
      htmlMain: 'ВРП, млрд. тенге',
      colorBlue: 'rgba(10, 10, 255, 0.5)',
      colorRed: 'rgba(255, 10, 25, 0.5)',
      colorYellow: 'rgba(255, 150, 0, 0.5)',
      myIndicator: 'ВРП, млрд. тенге'
    };
  },
  methods: {
    industryFilter(name, indicator){
        this.filterName = name
        this.basic = []
        this.optimistic = []
        this.pessimistic = []
        // basic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Базовый'
            }}).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == indicator && d["industry"] == this.filterName) {
                    this.basic.push(d['value'].toFixed(1))
                }
                // console.log(this.basic)
            })
        }).catch(error => console.log(error))
        // optimistic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Оптимистичный'
            }}).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == indicator && d["industry"] == this.filterName) {
                    this.optimistic.push(d['value'].toFixed(1))
                }
            })}).catch(error => console.log(error))
        // pessimistic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Пессимистичный'
            }}).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == indicator && d["industry"] == this.filterName) {
                    this.pessimistic.push(d['value'].toFixed(1))
                }
            })}).catch(error => console.log(error))
    },
    allData() {
        this.filterName = 'ВРП'
        this.myIndicator = 'ВРП, млрд. тенге'
        this.htmlMain = 'ВРП, млрд. тенге'
        this.basic = []
        this.optimistic = []
        this.pessimistic = []
        // basic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Базовый'
            }
        }).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП') {
                    this.basic.push(d['value'].toFixed(1))
                }
            })
        }).catch(error => console.log(error))
        // optimistic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Оптимистичный'
            }
        }).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП') {
                    this.optimistic.push(d['value'].toFixed(1))
                }
            })
        }).catch(error => console.log(error))
        // pessimistic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Пессимистичный'
            }
        }).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП') {
                    this.pessimistic.push(d['value'].toFixed(1))
                }
            })
        }).catch(error => console.log(error))
    },
    ifo() {
        if(this.filterName == 'По отраслям'){
            this.filterName = 'ВРП'
        }
        this.myIndicator = 'ИФО ВРП, %'
        this.htmlMain = 'ИФО ВРП, % к предыдущему году'
        this.basic = []
        this.optimistic = []
        this.pessimistic = []
        // basic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Базовый'
            }
        }).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == 'ИФО ВРП, %' && d["industry"] == this.filterName) {
                    this.basic.push(d['value'].toFixed(1))
                }
            })
        }).catch(error => console.log(error))
        // optimistic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Оптимистичный'
            }
        }).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == 'ИФО ВРП, %' && d["industry"] == this.filterName) {
                    this.optimistic.push(d['value'].toFixed(1))
                }
            })
        }).catch(error => console.log(error))
        // pessimistic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Пессимистичный'
            }
        }).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == 'ИФО ВРП, %' && d["industry"] == this.filterName) {
                    this.pessimistic.push(d['value'].toFixed(1))
                }
            })
        }).catch(error => console.log(error))
    },
    deflator() {
        if(this.filterName == 'По отраслям'){
            this.filterName = 'ВРП'
        }
        this.myIndicator = 'Дефлятор ВРП, %'
        this.htmlMain = 'Дефлятор ВРП, % к предыдущему году'
        this.basic = []
        this.optimistic = []
        this.pessimistic = []
            // basic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Базовый'
            }
        }).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == 'Дефлятор ВРП, %' && d["industry"] == this.filterName) {
                    this.basic.push(d['value'].toFixed(1))
                }
            })
        }).catch(error => console.log(error))
        // optimistic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Оптимистичный'
            }
        }).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == 'Дефлятор ВРП, %' && d["industry"] == this.filterName) {
                    this.optimistic.push(d['value'].toFixed(1))
                }
            })
        }).catch(error => console.log(error))
        // pessimistic
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: 'Пессимистичный'
            }
        }).then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d["indicator"] == 'Дефлятор ВРП, %' && d["industry"] == this.filterName) {
                    this.pessimistic.push(d['value'].toFixed(1))
                }
            })
        }).catch(error => console.log(error))
    },
    onlyUnique(value, index, self) { 
        return self.indexOf(value) === index;
    }
  },
  created() {
    // basic
    axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
        params: {
            scenario: 'Базовый'
        }
    }).then((arrRegions) => {
        arrRegions.data.map(d => {
            if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП') {
                this.years.push(d['year'])
                this.basic.push(d['value'].toFixed(1))
                if(d['industry'] !== 'ВРП'){
                    this.industries.push(d['industry'])
                }
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["year"] == '2025') {
                this.industries.push(d['industry'])
            }
        })
        this.industries = this.industries.filter(this.onlyUnique)
    }).catch(error => console.log(error))
    // optimistic
    axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
        params: {
            scenario: 'Оптимистичный'
        }
    }).then((arrRegions) => {
        arrRegions.data.map(d => {
            if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП') {
                this.optimistic.push(d['value'].toFixed(1))
            }
        })
    }).catch(error => console.log(error))
    // pessimistic
    axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
        params: {
            scenario: 'Пессимистичный'
        }
    }).then((arrRegions) => {
        arrRegions.data.map(d => {
            if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП') {
                this.pessimistic.push(d['value'].toFixed(1))
            }
        })
    }).catch(error => console.log(error))
  }
};
</script>

<style scoped>
    body{
        font-size: .7rem;
    }
    table {
        font-family: arial, sans-serif;
        border-collapse: collapse;
        width: 100%;
        font-size: .8rem;
    }

    td, th {
        border: 1px solid #ffd000;
        filterName-align: left;
        padding: 8px;
    }

    th {
        background-color: #ffd000;
    }

    tr:nth-child(even) {
        background-color: #dddddd;
    }

    .page-link {
        color: blue
    }

    .disabled {
        color: black
    }
</style>