<script>
import { Line } from "vue-chartjs";

export default {
  extends: Line,
  props: {
    label: {
      type: String
    },
    label2: {
      type: String
    },
    chartData: {
      type: Array 
    },
    chartData2: {
      type: Array 
    },
    chartLabels: {
      type: Array
    },
    options: {
      type: Object
    },
    chartColors: {
      type: Object
    }
  },
  mounted() {
    const {
      borderColor,
      pointBorderColor,
      pointBackgroundColor,
    } = this.chartColors;

    this.renderChart(
      {
        labels: this.chartLabels,
        datasets: [
          {
            label: this.label,
            data: this.chartData,
            borderColor: 'red',
            pointBorderColor: pointBorderColor,
            pointBackgroundColor: pointBackgroundColor,
            backgroundColor: 'rgba(54, 15, 255, 0.1)'
          },
          {
            label: this.label2,
            data: this.chartData2,
            borderColor: borderColor,
            pointBorderColor: pointBorderColor,
            pointBackgroundColor: pointBackgroundColor,
            backgroundColor: 'rgba(54, 255, 255, 0.1)'
          },
        ],
        options: {
                scales: {
                    xAxes: [{
                        ticks: {
                            min: 0
                        }
                    }]
                }
            }
      }
    )
  },
  watch: {
    chartData(newData) {
        this.chartData = newData
        const {
            borderColor,
            pointBorderColor,
            pointBackgroundColor,
            } = this.chartColors;

        this.renderChart(
        {
            labels: this.chartLabels,
            datasets: [
              {
                label: this.label,
                data: this.chartData,
                borderColor: 'red',
                pointBorderColor: pointBorderColor,
                pointBackgroundColor: pointBackgroundColor,
                backgroundColor: 'rgba(54, 15, 255, 0.1)'
              },
              {
                label: this.label2,
                data: this.chartData2,
                borderColor: borderColor,
                pointBorderColor: pointBorderColor,
                pointBackgroundColor: pointBackgroundColor,
                backgroundColor: 'rgba(54, 255, 255, 0.1)'
              }
            ],
            options: {
                scales: {
                    xAxes: [{
                        ticks: {
                            min: 0
                        }
                    }]
                }
            }
          }
      )
    }
  }
};
</script>