<template>
  <div class="container-fluid" style="width: 60vw;">
      <div v-if="firstPage">
        <div class="d-flex">
            <v-card style="width: 0px" to="/main/social-eco-dev"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="20px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg></v-card>
            <h3 class="mx-auto">Государственная инспекция труда</h3>
        </div>
        <div class="row d-flex">
                <div class="dropdown col-7">
                    <p>Показатель</p>
                    <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuIndex" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ htmlIndex }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuIndex" style="max-height: 200px; overflow-x: hidden;">
                        <a style="font-size: .7rem" @click="changeIndicator(i)" v-for="i in Object.keys(indicators)" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
                <div class="dropdown col-2 offset-3">
                    <p>Год</p>
                    <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ htmlYears }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        <a style="font-size: .7rem" @click="changeYear(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
        </div>
            <div class="row">
                <div class="col-12">
                    <HorizontalBar
                    :height="200"
                    :chartData="changeData"
                    :chartLabels="monthes"
                    :chartColors="regionsChartColors"
                    :label="htmlIndex"
                    />
                </div>
            </div>
      </div>
      <div v-if="secondPage">
        <div class="d-flex">
            <v-card style="width: 0px" to="/main/social-eco-dev"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="20px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg></v-card>
            <h3 class="mx-auto">Государственная инспекция труда</h3>
        </div>
        <div class="row d-flex">
                <div class="dropdown col-7">
                    <p>Показатель</p>
                    <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuIndex" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ htmlIndex2 }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuIndex" style="max-height: 200px; overflow-x: hidden;">
                        <a style="font-size: .7rem" @click="changeIndicator2(i)" v-for="i in Object.keys(indicators2)" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
                <div class="dropdown col-2 offset-3">
                    <p>Год</p>
                    <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ htmlYears2 }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        <a style="font-size: .7rem" @click="changeYear2(i)" v-for="i in years2" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
        </div>
            <div class="row">
                <div class="col-12">
                    <HorizontalBar
                    :height="200"
                    :chartData="changeData2"
                    :chartLabels="monthes2"
                    :chartColors="regionsChartColors"
                    :label="htmlIndex2"
                    />
                </div>
            </div>
      </div>
      <div v-if="thirdPage">
        <div class="d-flex">
            <v-card style="width: 0px" to="/main/social-eco-dev"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="20px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg></v-card>
            <h3 class="mx-auto">Государственная инспекция труда</h3>
        </div>
        <div class="row d-flex">
                <div class="dropdown col-7">
                    <p>Показатель</p>
                    <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuIndex" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ htmlIndex3 }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuIndex" style="max-height: 200px; overflow-x: hidden;">
                        <a style="font-size: .7rem" @click="changeIndicator3(i)" v-for="i in Object.keys(indicators3)" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
                <div class="dropdown col-2 offset-3">
                    <p>Год</p>
                    <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ htmlYears3 }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        <a style="font-size: .7rem" @click="changeYear3(i)" v-for="i in years3" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
        </div>
            <div class="row">
                <div class="col-12">
                    <HorizontalBar
                    :height="200"
                    :chartData="changeData3"
                    :chartLabels="monthes3"
                    :chartColors="regionsChartColors"
                    :label="htmlIndex3"
                    />
                </div>
            </div>
      </div>
            <h6 class="row text-center">{{ pageNum }}</h6>
        <ul class="pagination pagination-sm d-flex justify-center">
            <button @click="page1()" class="page-link">1</button>
            <button @click="page2()" class="page-link">2</button>
            <button @click="page3()" class="page-link">3</button>
        </ul>
  </div>
</template>

<script>
import axios from "axios";

import HorizontalBar from "./HorizontalBar";

export default {
  components: {
    HorizontalBar
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: "rgb(98, 153, 235)",
        pointHitRadius: 10
      },
      pageNum: 'Стр. 1',
      htmlYears: '2018',
      htmlYears2: '2018',
      htmlYears3: '2018',
      htmlIndex: 'Мониторинг коллективных договоров, единиц',
      htmlIndex2: 'Количество несчастных случаев, единиц',
      htmlIndex3: 'Наложено администратиынфх штрафов, единиц',
      regions: [],
      years: [],
      years2: [],
      years3: [],
      allData: [],
      allData2: [],
      allData3: [],
      changeData: [],
      changeData2: [],
      changeData3: [],
      indicators: {'Мониторинг коллективных договоров, единиц':'monitoringOfCollectiveAgreements', 'Наложено администратиынфх штрафов, единиц':'administrativeFinesImposed'},
      indicators2: {'Количество несчастных случаев, единиц':'numberOfAccidents', 'Количество несчастных случаев (Из них групповых), единиц':'numberOfTeamAccidents', 'Количество пострадавших, человек':'numberOfVictims', 'Количество пострадавших (Из них погибших), человек':'numberOfVictimsDead', 'Наложено административных штрафов, единиц':'administrativeFinesImposed'},
      indicators3: {'Наложено администратиынфх штрафов, единиц':'administrativeFinesImposed', 'Выявление фактов несвоевременной выплаты заработной платы, человек':'identificationUntimelyPayment', 'Социальная напряженность в трудовых коллективах, единиц':'socialTension'},
      monthes: [],
      monthes2: [],
      monthes3: [],
      firstPage: true,
      secondPage: false,
      thirdPage: false,
    };
  },
  methods: {
    page1() {
        this.pageNum = 'Стр. 1'
        this.firstPage = true
        this.secondPage = false
        this.thirdPage = false
    },
    page2() {
        this.pageNum = 'Стр. 2'
        this.firstPage = false
        this.secondPage = true
        this.thirdPage = false
    },
    page3() {
        this.pageNum = 'Стр. 3'
        this.firstPage = false
        this.secondPage = false
        this.thirdPage = true
    },
    changeIndicator(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData.map(d => d[key])
    },
    changeIndicator2(i) {
        this.htmlIndex2 = i
        let key = this.indicators2[this.htmlIndex2]
        this.changeData2 = this.allData2.map(d => d[key])
    },
    changeIndicator3(i) {
        this.htmlIndex3 = i
        let key = this.indicators3[this.htmlIndex3]
        this.changeData3 = this.allData3.map(d => d[key])
    },
    changeYear(year) {
        this.htmlYears = year
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/state-labor-inspectorate', {
            params: {
                year: year
            }
        }).then(d => {
            this.allData = d.data
            this.monthes = d.data.map(e => e.month)
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData.map(e => e[key])
        }).catch(err => console.log(err));
    },
    changeYear2(year) {
        this.htmlYears2 = year
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/stateokbot', {
            params: {
                year: year
            }
        }).then(d => {
            this.allData2 = d.data
            this.monthes2 = d.data.map(e => e.month)
            let key = this.indicators2[this.htmlIndex2]
            this.changeData2 = this.allData2.map(d => d[key])
        }).catch(err => console.log(err));
    },
    changeYear3(year) {
        this.htmlYears3 = year
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/stateokto', {
            params: {
                year: year
            }
        }).then(d => {
            this.allData3 = d.data
            this.monthes3 = d.data.map(e => e.month)
            let key = this.indicators3[this.htmlIndex3]
            this.changeData3 = this.allData3.map(d => d[key])
        }).catch(err => console.log(err));
    },
  },
  created() {
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/state-labor-inspectorate/years')
        .then(arrYears => {
            this.years = arrYears.data.sort()
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/state-labor-inspectorate', {
            params: {
                year: this.htmlYears
            }
        })
        .then(arrMonths => {
            this.monthes = arrMonths.data.map(d => d.month)
            this.allData = arrMonths.data   
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData.map(d => d[key])
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/stateokbot/years')
        .then(arrYears => {
            this.years2 = arrYears.data.sort()
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/stateokbot', {
            params: {
                year: this.htmlYears2
            }
        })
        .then(arrMonths => {
            this.monthes2 = arrMonths.data.map(d => d.month)
            this.allData2 = arrMonths.data   
            let key = this.indicators2[this.htmlIndex2]
            this.changeData2 = this.allData2.map(d => d[key])
        })
        .catch(error => console.log(error))
        
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/stateokto/years')
        .then(arrYears => {
            this.years3 = arrYears.data.sort()
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/stateokto', {
            params: {
                year: this.htmlYears3
            }
        })
        .then(arrMonths => {
            this.monthes3 = arrMonths.data.map(d => d.month)
            this.allData3 = arrMonths.data   
            let key = this.indicators3[this.htmlIndex3]
            this.changeData3 = this.allData3.map(d => d[key])
        })
        .catch(error => console.log(error))
  }
};
</script>