<template>
  <div class="container-fluid" style="width: 60vw;" v-if="allData">
      <div class="d-flex">
        <v-card style="width: 0px" to="/main/social-eco-dev"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="20px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg></v-card>
        <h3 class="mx-auto">Природные ресурсы и регулирование природопользования</h3>
      </div>
    <div class="row d-flex">
            <div class="dropdown col-5" v-if="MQIndicator">
                <p>Показатель</p>
                <button style="font-size: .7rem; max-width: 20vw; overflow-x: hidden" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" >
                    {{ htmlIndex }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow-x: hidden;">
                    <a style="font-size: .7rem" @click="changeIndicator(i)" v-for="i in Object.keys(indicators)" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown col-5" v-if="yearsIndicator">
                <p>Показатель</p>
                <button style="font-size: .7rem; max-width: 20vw; overflow-x: hidden" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" >
                    {{ htmlIndex }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow-x: hidden;">
                    <a style="font-size: .7rem" @click="changeIndicatorToYears(i)" v-for="i in Object.keys(indicators)" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="d-flex">
                <button class="btn btn-primary" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeMonthes()">Ежемесячные</button>
                <button class="btn btn-primary" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeQuarters()">Ежеквартальные</button>
                <button class="btn btn-primary" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeHalfs()">Полугодие</button>
                <button class="btn btn-primary" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeYears()"
                >Годовые</button>
            </div>
            <div class="dropdown col-1" v-if="yearsBtn1">
                <p>Год</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="changeYearByMonthes(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown col-1" v-if="yearsBtn2">
                <p>Год</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="changeYearByQuarters(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
    </div>
        <div class="row">
            <div class="col-12" v-if="MQChart">
                <HorizontalBar
                :height="200"
                :chartData="Object.values(changeData)"
                :chartLabels="Object.keys(changeData)"
                :chartColors="regionsChartColors"
                :label="htmlIndex"
                :options="chartOptions"
                />
            </div>
            <div class="col-12" v-if="yearsChart">
                <HorizontalBar
                :height="200"
                :chartData="changeData"
                :chartLabels="byYearsFromYears"
                :chartColors="regionsChartColors"
                :label="htmlIndex"
                :options="chartOptions"
                />
            </div>
        </div>
  </div>
</template>

<script>
import axios from "axios";

import HorizontalBar from "./HorizontalBar";

export default {
  components: {
    HorizontalBar
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: "rgb(98, 153, 235)",
        pointHitRadius: 10
      },
      chartOptions: {
          scales: {
            yAxes: [{
                ticks: {
                    beginAtZero: true,
                }
            }]
        }
      },
      htmlYears: '2019',
      htmlIndex: 'Обращение физических и юридичиских лиц',
      regions: [],
      years: ['2015', '2016', '2017', '2018', '2019', '2020'],
      allData: [],
      obj1: [],
      obj2: [],
      obj3: [],
      obj4: [],
      obj5: [],
      changeData: [],
      indicators: {},
      indicatorsByMonthes: {'Численность населения, тыс. человек':'numberPopulation', 'Количество созданных рабочих мест, единиц':'numberJobsCreated'},
      indicatorsByQuarters: {'Наемные работники, тыс. человек':'hiredWorker', 'Самостоятельно занятые, тыс. человек':'selfEmployed', 'Среднемесячная зарплата, тенге':'averageMonthlyWage', 'Численность пенсионеров, тыс. человек':'numberPensioners', 'Численность экономически активного населения, тыс. человек':'numberPopulation', 'Численность безработных, тыс. человек':'numberUnemployed', 'Количество получателей адресной социальной помощи, человек':'numberRecipientsAsp', 'Уровень безработицы, %':'nemploymentRate', 'Занятое население, тыс. человек':'employedPopulation', 'Доля трудоустроенных инвалидов от количество обратившихся инвалидов, %':'shareWithDisabilities', 'Доля населения с доходами ниже прожиточного минимума, %':'proportionPopulation', 'Прожиточный минимум':'subsistence'},

      indicatorsByHalfs: {'Обращение физических и юридичиских лиц':'appeal', 'Выдача заключений государственной экологической экспертизы для объектов 2,3 и 4 категорий, штук':'resultsStateExpertise', 'Выдача разрешений на эмиссии в окружающую среду для объектов 2,3 и 4 категорий, штук':'issuancePermitsEmissions', 'Проведение общественных слушаний':'conductingPublicHearings', 'Выдача лесорубочного и лесного билета, штук':'resultsTicket', 'Государственная регистрация договора долгосрочного лесопользования на участках государственного лесного фонда':'stateRegisContract', 'Заключение, регистрация и хранение контрактов на разведку, добычу общераспрастраненных полезных ископаемых, штук':'conclusionRegistration', 'Выдача письменного разрешения для получения права недропользования по разведке и добыче ОПИ, штук':'lssuanceWrittenTheopi', 'Объем накопленных твердо-бытовых отходов':'volumeWaste', 'Выполнение плана природоохранных мероприятий':'ilmplementationActionPlan', 'Истребление волков в природе':'exterminationWolvesNature'},

      indicatorsByYears: {'Посадка леса ГЛФ, га':'forestPlantingGlf', 'Содействие естественному возобновлению леса, га':'regenerationForests', 'Перевод лесных культур покрытые лесом площади, га':'transferForestcultures', 'Устройство минерализованных полос на территории ГЛФ, км':'deviceMineralizedstrips', 'Животный мир':'animalWorld', 'Регситрация договоров залога контрактов по ОПИ, штук':'registrationContracts'},
      yearsBtn1: true,
      yearsBtn2: false,
      yearsIndicator: false,
      MQIndicator: true,
      MQChart: true,
      yearsChart: false,
      byYearsFromYears: [],
      test: {}
    };
  },
  methods: {
    changeYears() {
        this.yearsBtn1 = false
        this.yearsBtn2 = false
        this.MQIndicator = false
        this.MQChart = false
        this.yearsIndicator = true
        this.yearsChart = true
        this.htmlIndex = 'Посадка леса ГЛФ, га'
        this.indicators = this.indicatorsByYears

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/natural', {
            params: {
                byYears: true
            }
        })
        .then(byYears => {
            this.byYearsFromYears = byYears.data.map(d => d.year)
            this.obj1 = byYears.data
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/naturals', {
            params: {
                byYears: true
            }
        })
        .then(byYears => {
            this.obj2 = byYears.data
            this.allData = Object.assign(this.obj1, this.obj2)
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData.map(d => d[key])
            // console.log(this.byYearsFromYears)
        })
        .catch(error => console.log(error))
    },
    changeHalfs() {
        this.yearsBtn1 = false
        this.yearsBtn2 = true
        this.MQIndicator = true
        this.MQChart = true
        this.yearsIndicator = false
        this.yearsChart = false
        this.htmlIndex = 'Обращение физических и юридичиских лиц'
        this.htmlYears = '2019'
        this.allData = []
        this.changeData = []

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/nathoapkr', {
            params: {
                year: this.htmlYears,
                byHalfs: true}}).then(byHalfs => this.obj1 = byHalfs.data).catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/nathogeevr', {
            params: {
                year: this.htmlYears,
                byHalfs: true}}).then(byHalfs => this.obj2 = byHalfs.data).catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/natholoh', {
            params: {
                year: this.htmlYears,
                byHalfs: true}}).then(byHalfs => this.obj3 = byHalfs.data).catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/nathonvh', {
            params: {
                year: this.htmlYears,
                byHalfs: true}}).then(byHalfs => this.obj4 = byHalfs.data).catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/nathopp', {
            params: {
                year: this.htmlYears,
                byHalfs: true
            }
        })
        .then(byHalfs => {
            this.indicators = this.indicatorsByHalfs
            this.obj5 = byHalfs.data
            this.allData = Object.assign(this.obj1, this.obj2, this.obj3, this.obj4, this.obj5)
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData[key]
        })
        .catch(error => console.log(error))
    },
    changeQuarters() {
        this.yearsBtn1 = false
        this.yearsBtn2 = true
        this.MQIndicator = true
        this.MQChart = true
        this.yearsIndicator = false
        this.yearsChart = false
        this.htmlIndex = 'Самостоятельно занятые, тыс. человек'
        this.htmlYears = '2019'
        this.allData = []
        this.changeData = []

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/koord/years')
        .then(arrYears => {
            this.years = arrYears.data["Года мониторинга Координации по кварталам"].sort()
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/koord', {
            params: {
                year: this.htmlYears,
                byQuarters: true
            }
        })
        .then(arrMonths => {
            this.indicators = this.indicatorsByQuarters
            this.allData = arrMonths.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData[key]
        })
        .catch(error => console.log(error))
    },
    changeMonthes() {
        this.yearsBtn1 = true
        this.yearsBtn2 = false
        this.MQIndicator = true
        this.MQChart = true
        this.yearsIndicator = false
        this.yearsChart = false
        this.htmlIndex = 'Численность населения, тыс. человек'

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/koord/years')
        .then(arrYears => {
            this.years = arrYears.data["Года мониторинга Координации по месяцам"].sort()
            this.htmlYears = this.years[this.years.length - 1]
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/koord', {
            params: {
                year: this.htmlYears,
                byMonthes: true
            }
        })
        .then(arrMonths => {
            this.indicators = this.indicatorsByMonthes
            this.allData = arrMonths.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData[key]
        })
        .catch(error => console.log(error))
    },
    changeIndicator(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData[key]
    },
    changeIndicatorToYears(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData.map(d=>d[key])
    },
    changeYearByMonthes(year) {
        this.htmlYears = year
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/koord', {
            params: {
                year: year,
                byMonthes: true
            }
        }).then(d => {
            this.indicators = this.indicatorsByMonthes
            this.allData = d.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData[key]
        }).catch(err => console.log(err));
    },
    changeYearByQuarters(year) {
        this.htmlYears = year
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/koord', {
            params: {
                year: year,
                byQuarters: true
            }
        }).then(d => {
            this.indicators = this.indicatorsByQuarters
            this.allData = d.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData[key]
        }).catch(err => console.log(err));
    }
  },
  created() {
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/nathoapkr', {
            params: {
                year: this.htmlYears,
                byHalfs: true}}).then(byHalfs => this.obj1 = byHalfs.data).catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/nathogeevr', {
            params: {
                year: this.htmlYears,
                byHalfs: true}}).then(byHalfs => this.obj2 = byHalfs.data).catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/natholoh', {
            params: {
                year: this.htmlYears,
                byHalfs: true}}).then(byHalfs => this.obj3 = byHalfs.data).catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/nathonvh', {
            params: {
                year: this.htmlYears,
                byHalfs: true}}).then(byHalfs => this.obj4 = byHalfs.data).catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/nathopp', {
            params: {
                year: this.htmlYears,
                byHalfs: true
            }
        })
        .then(byHalfs => {
            this.indicators = this.indicatorsByHalfs
            this.obj5 = byHalfs.data
            this.allData = Object.assign(this.obj1, this.obj2, this.obj3, this.obj4, this.obj5)
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData[key]
            console.log(this.changeData)
        })
        .catch(error => console.log(error))
  }
};
</script>