      htmlScenario: 'Базовый',
      scenaries: ['Базовый', 'Оптимистичный', 'Пессимистичный'],
      myIndicator: 'ВРП, млрд. тенге',
      htmlIndicator: 'ВРП, млрд. тенге',
      years: [],
      goods: [],
      industry: [],
      waterSupply: [],
      mining: [],
      manufacturing: [],
      electricity: [],
      agriculture: [],
      building: [],
      service: [],
      communication: [],
      estates: [],
      other: [],
      wholesale: [],
      retail: [],
      transport: [],
      taxes: [],
      tableAll: true,
      tableProduction: false,
      tableGoods: false,
      tableServices: false,
      tableIndustries: false,
      myBtn: true
    };
  },
  methods: {
    ifo(){
        this.myBtn = false
        this.tableAll = true
        this.tableProduction = false
        this.tableGoods = false
        this.tableServices = false
        this.tableIndustries = false
        this.myIndicator = 'ИФО ВРП, %'
        this.htmlIndicator = 'ИФО ВРП, %'
        this.goods = []
        this.industry = []
        this.waterSupply = []
        this.mining = []
        this.manufacturing = []
        this.electricity = []
        this.agriculture = []
        this.building = []
        this.service = []
        this.communication = []
        this.estates = []
        this.other = []
        this.wholesale = []
        this.retail = []
        this.transport = []
        this.taxes = []
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
        params: {
            scenario: this.htmlScenario
            }
        })
        .then((arrRegions) => {
            arrRegions.data.map(d => {
                if (d['indicator'] == this.myIndicator && d['industry'] == 'Производство товаров' && d['investments'] == 'Нет') {
                    this.goods.push(d['value'])
                }
                else if (d['indicator'] == this.myIndicator && d['industry'] == 'Промышленность' && d['investments'] == 'Нет') {
                    this.industry.push(d['value'])
                }
                else if (d['indicator'] == this.myIndicator && d['industry'] == 'Водоснабжение; канализационная система, контроль над сбором и распределением отходов' && d['investments'] == 'Нет') {
                    this.waterSupply.push(d['value'])
                }
