<template>
  <div class="container-fluid" style="width: 60vw;" v-if="allData">
      <div class="d-flex">
        <v-card style="width: 0px" to="/main/social-eco-dev"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="20px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg></v-card>
        <h3 class="mx-auto">Здравоохранение</h3>
      </div>
    <div class="row d-flex">
            <div class="dropdown col-7">
                <p>Показатель</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuIndex" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlIndex }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuIndex">
                    <a style="font-size: .7rem" @click="changeIndicator(i)" v-for="i in Object.keys(indicators)" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown col-2 offset-3">
                <p>Год</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="changeYear(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
    </div>
        <div class="row">
            <div class="col-12">
                <HorizontalBar
                :height="200"
                :chartData="Object.values(changeData)"
                :chartLabels="Object.keys(changeData)"
                :chartColors="regionsChartColors"
                :label="htmlIndex"
                />
            </div>
        </div>
  </div>
</template>

<script>
import axios from "axios";

import HorizontalBar from "./HorizontalBar";

import LineChart from "./Line-chart";

export default {
  components: {
    HorizontalBar,
    LineChart
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: "rgb(98, 153, 235)",
        pointHitRadius: 10
      },
      htmlYears: '2020',
      htmlIndex: 'Прием документов в архив, единиц',
      regions: [],
      years: ['2018', '2019', '2020'],
      allData: [],
      obj1: [],
      obj2: [],
      changeData: [],
      indicators: {'Прием документов в архив, единиц':'receptionDocumentsArchive', 'Перевод архивных документов в цифровой формат, единиц':'translationArchivalDocuments', 'Доля документов, переведенных в цифровой формат, %':'proportionDocuments', 'Мероприятия,проведенные в театре и филармонии (спекталь, концерт), единиц':'issuanceArchivalCertificates', 'Тематические запросы, единиц':'thematicRequests', 'Запросы социально-правового характера, единиц':'socialLegalInquiries', 'Уровень посещаемости объектов культуры населением, человек':'attendanceCulturalObjectsPopulation', 'Оказание государственной услуги "Выдача архивных справок", количество запросов, единиц':'eventsHeldTheatre', 'Проведенные мероприятия по клубным учреждениям, единиц':'clubEvents', 'Количество объектов клубов, единиц':'objectsClub', 'Количество объектов культуры, единиц':'numberCulturalObjects', 'Количество объектов библиотек, единиц':'numberLibraryObjects', 'Количество объектов музеев, единиц':'numberMuseumObjects', 'Количество объектов парков, единиц':'numberParkObjects', 'Количество аварийных объектов культуры, единиц':'numberEmergencyObjectsCulture'},
    //   indicators1: {'Прием документов в архив, единиц':'receptionDocumentsArchive', 'Перевод архивных документов в цифровой формат, единиц':'translationArchivalDocuments', 'Доля документов, переведенных в цифровой формат, %':'proportionDocuments', 'Мероприятия,проведенные в театре и филармонии (спекталь, концерт), единиц':'issuanceArchivalCertificates', 'Тематические запросы, единиц':'thematicRequests', 'Запросы социально-правового характера, единиц':'socialLegalInquiries'},

    //   indicators2: {'Уровень посещаемости объектов культуры населением, человек':'attendanceCulturalObjectsPopulation', 'Оказание государственной услуги "Выдача архивных справок", количество запросов, единиц':'eventsHeldTheatre', 'Проведенные мероприятия по клубным учреждениям, единиц':'clubEvents', 'Количество объектов клубов, единиц':'objectsClub', 'Количество объектов культуры, единиц':'numberCulturalObjects', 'Количество объектов библиотек, единиц':'numberLibraryObjects', 'Количество объектов музеев, единиц':'numberMuseumObjects', 'Количество объектов парков, единиц':'numberParkObjects', 'Количество аварийных объектов культуры, единиц':'numberEmergencyObjectsCulture'},
      indexKey: 'Кол-во санитарных машин, единиц'
    };
  },
  methods: {
    changeIndicator(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData[key]
    },
    changeYear(year) {
        this.htmlYears = year
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/archy', {
            params: {
                year: this.htmlYears,
                byHalfs: true
            }
        })
        .then(archData => {
            this.obj1 = archData.data
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/kulthy', {
            params: {
                year: this.htmlYears,
                byHalfs: true
            }
        })
        .then(kulthyData => {
            this.obj2 = kulthyData.data
            this.allData = Object.assign(this.obj1, this.obj2)
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData[key]
        })
        .catch(error => console.log(error))
    }
  },
  created() {
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/archy', {
            params: {
                year: this.htmlYears,
                byHalfs: true
            }
        })
        .then(archData => {
            this.obj1 = archData.data
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/kulthy', {
            params: {
                year: this.htmlYears,
                byHalfs: true
            }
        })
        .then(kulthyData => {
            this.obj2 = kulthyData.data
            this.allData = Object.assign(this.obj1, this.obj2)
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData[key]
        })
        .catch(error => console.log(error))
  }
};
</script>