<template>
  <div class="container-fluid" style="width: 70vw;" v-if="htmlRegions">
        <h5 class="text-center">Доля районов в валовом выпуске продукции области, %</h5>
        <div class="d-flex">
            <div class="dropdown offset-10">
                <button style="font-size: .7rem" class="btn text-white btn-primary dropdown-toggle mr-4" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlScenario }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="viewScena(i)" v-for="i in scenaries" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown">
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="viewYear(year)" v-for="year in years" :key="year" class="dropdown-item">{{ year }}</a>
                </div>
            </div>
        </div>
        <div class="row">
            <div class="col-6 offset-6">
                <BarChartFirst
                :height="220"
                :chartData="[atirau, zhylyoi, inder, isatai, kurmangazy, kzylkoga, makat, mahambet]"
                :chartLabels="regions"
                :chartColors="regionsChartColors"
                label="Валовой выпуск по районам, %"
                />
            </div>
        </div>
        <div class="row">
            <div class="col-6" style="max-height: 40vh; overflow: scroll; font-size: .8rem">
                    <table class="table">
                        <tr style="position: sticky">
                            <th>Регион</th>
                            <th v-for="i in table">{{ i }}</th>
                        </tr>
                        <tr v-for="item in test">
                            <td>{{ item.region }}</td>
                            <td >{{ (item.industry["Промышленность"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Электроснабжение, подача газа, пара и воздушное кондиционирование"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Строительство"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Оптовая торговля"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Всего"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Розничная торговля"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Сельское, лесное и рыбное хозяйство"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Обрабатывающая промышленность"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Водоснабжение; канализационная система, контроль над сбором и распределением отходов"] / 1000).toFixed(1) }}</td>
                            <td >{{ (item.industry["Производство товаров"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Горнодобывающая промышленность и разработка карьеров"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Производство услуг"] / 1000).toFixed(2) }}</td>
                            <td >{{ (item.industry["Оптовая и розничная торговля; ремонт автомобилей и мотоциклов"] / 1000).toFixed(2) }}</td>
                        </tr>
                    </table>
            </div>
            <div class="col-6">
                <BarChart
                :height="220"
                :chartData="ifo"
                :chartData2="deflator"
                :chartLabels="regions"
                :chartColors="regionsChartColors"
                label="ИФО ВВ, %"
                label2="Дефлятор ВВ, %"
                />
            </div>
        </div>
        <ul class="pagination pagination-sm d-flex justify-center">
            <v-card class="page-link" to="1">1</v-card>
            <v-card class="page-link" to="2">2</v-card>
            <v-card class="page-link" to="3">3</v-card>
            <v-card class="page-link disabled" to="4">4</v-card>
            <v-card class="page-link" to="5">5</v-card>
            <v-card class="page-link" to="6">6</v-card>
            <v-card class="page-link" to="7">7</v-card>
            <v-card class="page-link" to="8">8</v-card>
            <v-card class="page-link" to="9">9</v-card>
        </ul>
  </div>
</template>

<script>
import axios from "axios";

import BarChart from "./BarChart";

import BarChartFirst from "./BarChartFirst";

export default {
  components: {
    BarChart,
    BarChartFirst
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: '#06d6a0',
        backgroundColor2: '#118ab2',
        pointHitRadius: 10,
        options: {
            legend: {
                align: 'end'
            }
        }
      },
      htmlYears: '2019',
      htmlScenario: 'Базовый',
      scenaries: ["Базовый", "Оптимистичный", "Пессимистичный"],
      htmlRegions: new Set(),
      million: [],
      years: [],
      test: [],
      regions: [],
      ifo: [],
      deflator: [],
      table: [],
      tableRegion: [],
      atirau: [],
      zhylyoi: [],
      inder: [],
      isatai: [],
      kurmangazy: [],
      kzylkoga: [],
      makat: [],
      mahambet: []
    };
  },
  methods: {
    onlyUnique(value, index, self) { 
        return self.indexOf(value) === index;
    },
    viewYear(year) {
        this.htmlYears = year
        this.ifo = []
        this.deflator = []
        this.atirau = []
        this.zhylyoi = []
        this.inder = []
        this.isatai = []
        this.kurmangazy = []
        this.kzylkoga = []
        this.makat = []
        this.mahambet = []

        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpraion', {
        params: {
            year: this.htmlYears
            }
        })
        .then((arrRegions) => {
            arrRegions.data.map(d => {
                if(d['indicator'] == 'ИФО ВВ, %' && d["scenario"] == 'Базовый'
                && d["industry"] == 'Всего') {
                    this.ifo.push(d['value'].toFixed(1))
                }
                else if(d['indicator'] == 'Дефлятор ВВ, %' && d["scenario"] == 'Базовый'
                && d["industry"] == 'Всего') {
                    this.deflator.push(d['value'].toFixed(1))
                }
                else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Атырау' && d["scenario"] == 'Базовый'
                && d["industry"] !== 'Всего') {
                    if(d["industry"] !== 'Производство товаров'){
                        if(d["industry"] !== 'Производство услуг'){
                            if(d["industry"] !== 'Промышленность'){
                                if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                    this.atirau.push(d['value'])
                                }
                            }
                        }
                    }
                }
                else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Жылыой' && d["scenario"] == 'Базовый'
                && d["industry"] !== 'Всего') {
                    if(d["industry"] !== 'Производство товаров'){
                        if(d["industry"] !== 'Производство услуг'){
                            if(d["industry"] !== 'Промышленность'){
                                if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                    this.zhylyoi.push(d['value'])
                                }
                            }
                        }
                    }
                }
                else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Индер' && d["scenario"] == 'Базовый'
                && d["industry"] !== 'Всего') {
                    if(d["industry"] !== 'Производство товаров'){
                        if(d["industry"] !== 'Производство услуг'){
                            if(d["industry"] !== 'Промышленность'){
                                if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                    this.inder.push(d['value'])
                                }
                            }
                        }
                    }
                }
                else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Исатай' && d["scenario"] == 'Базовый'
                && d["industry"] !== 'Всего') {
                    if(d["industry"] !== 'Производство товаров'){
                        if(d["industry"] !== 'Производство услуг'){
                            if(d["industry"] !== 'Промышленность'){
                                if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                    this.isatai.push(d['value'])
                                }
                            }
                        }
                    }
                }
                else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Курмангазы' && d["scenario"] == 'Базовый'
                && d["industry"] !== 'Всего') {
                    if(d["industry"] !== 'Производство товаров'){
                        if(d["industry"] !== 'Производство услуг'){
                            if(d["industry"] !== 'Промышленность'){
                                if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                    this.kurmangazy.push(d['value'])
                                }
                            }
                        }
                    }
                }
                else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Кзылкога' && d["scenario"] == 'Базовый'
                && d["industry"] !== 'Всего') {
                    if(d["industry"] !== 'Производство товаров'){
                        if(d["industry"] !== 'Производство услуг'){
                            if(d["industry"] !== 'Промышленность'){
                                if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                    this.kzylkoga.push(d['value'])
                                }
                            }
                        }
                    }
                }
                else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Макат' && d["scenario"] == 'Базовый'
                && d["industry"] !== 'Всего') {
                    if(d["industry"] !== 'Производство товаров'){
                        if(d["industry"] !== 'Производство услуг'){
                            if(d["industry"] !== 'Промышленность'){
                                if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                    this.makat.push(d['value'])
                                }
                            }
                        }
                    }
                }
                else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Махамбет' && d["scenario"] == 'Базовый'
                && d["industry"] !== 'Всего') {
                    if(d["industry"] !== 'Производство товаров'){
                        if(d["industry"] !== 'Производство услуг'){
                            if(d["industry"] !== 'Промышленность'){
                                if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                    this.mahambet.push(d['value'])
                                }
                            }
                        }
                    }
                }
            })
            let arr = []
            this.atirau = Object.values(this.atirau).reduce((a, b) => a + b, 0) / 1000
            this.zhylyoi = Object.values(this.zhylyoi).reduce((a, b) => a + b, 0) / 1000
            this.inder = Object.values(this.inder).reduce((a, b) => a + b, 0) / 1000
            this.isatai = Object.values(this.isatai).reduce((a, b) => a + b, 0) / 1000
            this.kurmangazy = Object.values(this.kurmangazy).reduce((a, b) => a + b, 0) / 1000
            this.kzylkoga = Object.values(this.kzylkoga).reduce((a, b) => a + b, 0) / 1000
            this.makat = Object.values(this.makat).reduce((a, b) => a + b, 0) / 1000
            this.mahambet = Object.values(this.mahambet).reduce((a, b) => a + b, 0) / 1000
            arr.push(this.atirau)
            arr.push(this.zhylyoi)
            arr.push(this.inder)
            arr.push(this.isatai)
            arr.push(this.kurmangazy)
            arr.push(this.kzylkoga)
            arr.push(this.makat)
            arr.push(this.mahambet)
            this.atirau = ((parseFloat(this.atirau) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
            this.zhylyoi = ((parseFloat(this.zhylyoi) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
            this.inder = ((parseFloat(this.inder) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
            this.isatai = ((parseFloat(this.isatai) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
            this.kurmangazy = ((parseFloat(this.kurmangazy) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
            this.kzylkoga = ((parseFloat(this.kzylkoga) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
            this.makat = ((parseFloat(this.makat) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
            this.mahambet = ((parseFloat(this.mahambet) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrptables', {
        params: {
            year: this.htmlYears,
            scenario: this.htmlScenario,
            indicator: 'ИФО ВВ, %'
        }})
        .then(d => {
            this.test = d.data
        })
        .catch(err => console.log(err))
    },
    viewScena(scenario) {
        this.htmlScenario = scenario

        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrptables', {
        params: {
            year: this.htmlYears,
            scenario: this.htmlScenario,
            indicator: 'Выпуск продукции, млн. тенге'
        }})
        .then(d => {
            this.test = d.data
        })
        .catch(err => console.log(err))
    },
  },
  created() {
    axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpraionscenario', {
        params: {
            scenario: 'Базовый'
        }
        })
        .then(d => {
            d.data.map(a => {
                if(a['indicator'] == 'ИФО ВВ, %' && a["scenario"] == 'Базовый'
                && a["industry"] == 'Всего') {
                    this.years.push(a['year'])
                }
            })
            this.years = this.years.filter(this.onlyUnique)
            this.years = this.years.slice(this.years.length - 15, this.years.length - 0)
        })
        .catch(err => console.log(err))

    axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpraion', {
        params: {
            year: this.htmlYears
        }
    })
    .then((arrRegions) => {
        arrRegions.data.map(d => {
            if(d['indicator'] == 'ИФО ВВ, %' && d["scenario"] == 'Базовый'
            && d["industry"] == 'Всего') {
                this.regions.push(d['region'])
                this.ifo.push(d['value'].toFixed(1))
            }
            else if(d['indicator'] == 'Дефлятор ВВ, %' && d["scenario"] == 'Базовый'
            && d["industry"] == 'Всего') {
                this.deflator.push(d['value'].toFixed(1))
            }
            else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Атырау' && d["scenario"] == 'Базовый'
            && d["industry"] !== 'Всего') {
                if(d["industry"] !== 'Производство товаров'){
                    if(d["industry"] !== 'Производство услуг'){
                        if(d["industry"] !== 'Промышленность'){
                            if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                this.atirau.push(d['value'])
                            }
                        }
                    }
                }
            }
            else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Жылыой' && d["scenario"] == 'Базовый'
            && d["industry"] !== 'Всего') {
                if(d["industry"] !== 'Производство товаров'){
                    if(d["industry"] !== 'Производство услуг'){
                        if(d["industry"] !== 'Промышленность'){
                            if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                this.zhylyoi.push(d['value'])
                            }
                        }
                    }
                }
            }
            else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Индер' && d["scenario"] == 'Базовый'
            && d["industry"] !== 'Всего') {
                if(d["industry"] !== 'Производство товаров'){
                    if(d["industry"] !== 'Производство услуг'){
                        if(d["industry"] !== 'Промышленность'){
                            if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                this.inder.push(d['value'])
                            }
                        }
                    }
                }
            }
            else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Исатай' && d["scenario"] == 'Базовый'
            && d["industry"] !== 'Всего') {
                if(d["industry"] !== 'Производство товаров'){
                    if(d["industry"] !== 'Производство услуг'){
                        if(d["industry"] !== 'Промышленность'){
                            if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                this.isatai.push(d['value'])
                            }
                        }
                    }
                }
            }
            else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Курмангазы' && d["scenario"] == 'Базовый'
            && d["industry"] !== 'Всего') {
                if(d["industry"] !== 'Производство товаров'){
                    if(d["industry"] !== 'Производство услуг'){
                        if(d["industry"] !== 'Промышленность'){
                            if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                this.kurmangazy.push(d['value'])
                            }
                        }
                    }
                }
            }
            else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Кзылкога' && d["scenario"] == 'Базовый'
            && d["industry"] !== 'Всего') {
                if(d["industry"] !== 'Производство товаров'){
                    if(d["industry"] !== 'Производство услуг'){
                        if(d["industry"] !== 'Промышленность'){
                            if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                this.kzylkoga.push(d['value'])
                            }
                        }
                    }
                }
            }
            else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Макат' && d["scenario"] == 'Базовый'
            && d["industry"] !== 'Всего') {
                if(d["industry"] !== 'Производство товаров'){
                    if(d["industry"] !== 'Производство услуг'){
                        if(d["industry"] !== 'Промышленность'){
                            if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                this.makat.push(d['value'])
                            }
                        }
                    }
                }
            }
            else if(d['indicator'] == 'Выпуск продукции, млн. тенге' && d["region"] == 'Махамбет' && d["scenario"] == 'Базовый'
            && d["industry"] !== 'Всего') {
                if(d["industry"] !== 'Производство товаров'){
                    if(d["industry"] !== 'Производство услуг'){
                        if(d["industry"] !== 'Промышленность'){
                            if(d["industry"] !== 'Оптовая и розничная торговля; ремонт автомобилей и мотоциклов'){
                                this.mahambet.push(d['value'])
                            }
                        }
                    }
                }
            }
        })
        let arr = []
        this.atirau = Object.values(this.atirau).reduce((a, b) => a + b, 0) / 1000
        this.zhylyoi = Object.values(this.zhylyoi).reduce((a, b) => a + b, 0) / 1000
        this.inder = Object.values(this.inder).reduce((a, b) => a + b, 0) / 1000
        this.isatai = Object.values(this.isatai).reduce((a, b) => a + b, 0) / 1000
        this.kurmangazy = Object.values(this.kurmangazy).reduce((a, b) => a + b, 0) / 1000
        this.kzylkoga = Object.values(this.kzylkoga).reduce((a, b) => a + b, 0) / 1000
        this.makat = Object.values(this.makat).reduce((a, b) => a + b, 0) / 1000
        this.mahambet = Object.values(this.mahambet).reduce((a, b) => a + b, 0) / 1000
         arr.push(this.atirau)
         arr.push(this.zhylyoi)
         arr.push(this.inder)
         arr.push(this.isatai)
         arr.push(this.kurmangazy)
         arr.push(this.kzylkoga)
         arr.push(this.makat)
         arr.push(this.mahambet)
        this.atirau = ((parseFloat(this.atirau) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
        this.zhylyoi = ((parseFloat(this.zhylyoi) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
        this.inder = ((parseFloat(this.inder) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
        this.isatai = ((parseFloat(this.isatai) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
        this.kurmangazy = ((parseFloat(this.kurmangazy) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
        this.kzylkoga = ((parseFloat(this.kzylkoga) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
        this.makat = ((parseFloat(this.makat) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
        this.mahambet = ((parseFloat(this.mahambet) / (arr.reduce((a, b) => a + b, 0))) * 100).toFixed(2)
    })
    .catch(error => console.log(error))

    axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrptables', {
        params: {
            year: '2019',
            scenario: 'Базовый',
            indicator: 'Выпуск продукции, млн. тенге'
        }
        })
        .then(d => {
            this.test = d.data
            this.table = Object.keys(this.test[0]["industry"])
        })
        .catch(err => console.log(err))
    }
};
