<template>
  <div class="container-fluid" style="width: 60vw;" >
      <div class="d-flex">
        <v-card style="width: 0px" to="/main/social-eco-dev"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="20px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg></v-card>
        <h3 class="mx-auto">Развитие языков</h3>
      </div>
    <div class="row d-flex">
            <div class="dropdown col-5" v-if="MQIndicator">
                <p>Показатель</p>
                <button style="font-size: .7rem;" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlIndex }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow-x: hidden;">
                    <a style="font-size: .7rem" @click="changeIndicator(i)" v-for="i in Object.keys(indicators)" :title="i" :key="i"  class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown col-5" v-if="yearsIndicator">
                <p>Показатель</p>
                <button style="font-size: .7rem;" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlIndex }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow-x: hidden;">
                    <a style="font-size: .7rem" @click="changeIndicatorToYears(i)" v-for="i in Object.keys(indicators)" :title="i" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <button class="btn btn-primary col-2 offset-2" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeQuarters()">Ежеквартальные</button>
            <button class="btn btn-primary col-1" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeYears()"
            >Годовые</button>
            <div class="dropdown col-1" v-if="yearsBtn1">
                <p>Год</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="changeYearByQuarters(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown col-1" v-if="yearsBtn2">
                <p>Год</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="changeYearByQuarters(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
    </div>
        <div class="row">
            <div class="col-12" v-if="MQChart">
                <HorizontalBar
                :height="200"
                :chartData="Object.values(changeData)"
                :chartLabels="Object.keys(changeData)"
                :chartColors="regionsChartColors"
                :label="htmlIndex"
                :options="chartOptions"
                />
            </div>
            <div class="col-12" v-if="yearsChart">
                <HorizontalBar
                :height="200"
                :chartData="Object.values(changeData)"
                :chartLabels="chartYears"
                :chartColors="regionsChartColors"
                :label="htmlIndex"
                :options="chartOptions"
                />
            </div>
        </div>
  </div>
</template>

<script>
import axios from "axios";

import HorizontalBar from "./HorizontalBar";

export default {
  components: {
    HorizontalBar
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: "rgb(98, 153, 235)",
        pointHitRadius: 10
      },
      chartOptions: {
        scales: {
        yAxes: [{
            ticks: {
                beginAtZero: true,
                max: 120
                    }
                }]
        }
      },
      htmlYears: '2020',
      htmlIndex: 'Доля государственного языка в общем количестве внутренних исходящих документов, %',
      regions: [],
      years: [2012, 2013, 2014, 2015, 2016, 2017, 2018, 2019, 2020],
      allData: [],
      changeData: [],
      indicators: {},
      indicatorsByQuarters: {'Доля государственного языка в общем количестве внутренних исходящих документов, %':'shareDomesticOutgoingDocuments', 'Доля государственного языка в общем количестве входящих документов, %':'shareIncomingDocuments', 'Доля государственного языка в общем количестве исходящих документов, %':'shareOutgoingDocuments'},

      indicatorsByYears: {'Доля населения, владеющего государственным языком, %':'populationSpeakingLanguage'},
      yearsBtn1: true,
      yearsBtn2: false,
      yearsIndicator: false,
      MQIndicator: true,
      MQChart: true,
      yearsChart: false,
      chartYears: []
    };
  },
  methods: {
    changeQuarters() {
        this.htmlIndex = 'Доля государственного языка в общем количестве внутренних исходящих документов, %'
        this.yearsBtn1 = true
        this.yearsBtn2 = false
        this.MQIndicator = true
        this.MQChart = true
        this.yearsIndicator = false
        this.yearsChart = false

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/langq', {
            params: {
                year: this.htmlYears,
                byQuarters: true
            }
        })
        .then(arrMonths => {
            this.indicators = this.indicatorsByQuarters 
            this.allData = arrMonths.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData[key]
            
        })
        .catch(error => console.log(error))
    },
    changeYears() {
        this.htmlIndex = 'Доля населения, владеющего государственным языком, %'
        this.yearsBtn1 = false
        this.yearsBtn2 = false
        this.MQIndicator = false
        this.MQChart = false
        this.yearsIndicator = true
        this.yearsChart = true
        this.indicators = this.indicatorsByYears

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/lang', {
            params: {
                byYears: true
            }
        })
        .then(arrMonths => {
            this.chartYears = arrMonths.data.map(d => d['year'])
            this.allData = arrMonths.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData.map(d => d[key])
        })
        .catch(error => console.log(error))
    },
    changeIndicator(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData[key]
    },
    changeIndicatorToYears(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData[key]
    },
    changeYearByQuarters(year) {
        this.htmlYears = year
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/langq', {
            params: {
                year: year,
                byQuarters: true
            }
        }).then(d => {
            this.indicators = this.indicatorsByQuarters
            this.allData = d.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData[key]
        }).catch(err => console.log(err));
    },
  },
  created() {
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/lang', {
            params: {
                byYears: true
            }
        })
        .then(arrYears => {
            // this.years = arrYears.data.map(d => d['year']) 
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/langq', {
            params: {
                year: this.htmlYears,
                byQuarters: true
            }
        })
        .then(arrMonths => {
            this.indicators = this.indicatorsByQuarters
            this.allData = arrMonths.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = arrMonths.data[key]
            // console.log(this.changeData)
        })
        .catch(error => console.log(error))
  }
};
</script>

<style scoped>
    .nav-link{
        font-size: .7rem;
    }
</style>