<script>
import { Line } from "vue-chartjs";
import ChartJsPluginDataLabels from 'chartjs-plugin-datalabels'
Chart.helpers.merge(Chart.defaults.global.plugins.datalabels, {
    color: 'black',
    anchor: 'center',
    align: 'end',
    labels: {
      title: {
          font: {
              weight: 'bold',
              size: 10
          }
      },
    }
});

export default {
  plugins: [ChartJsPluginDataLabels],
  extends: Line,
  props: {
    label: {
      type: String
    },
    chartData: {
    },
    chartLabels: {
      type: Array
    },
    options: {
      type: Object
    },
    chartColors: {
      type: Object
    },
    currentColor: {
      type: String
    }
  },
  mounted() {
    const {
      borderColor,
      pointBorderColor,
      pointBackgroundColor,
      options
    } = this.chartColors;

    this.renderChart(
      {
        labels: this.chartLabels,
        datasets: [
          {
            label: this.label,
            data: this.chartData,
            borderColor: this.currentColor,
            pointBorderColor: pointBorderColor,
            pointBackgroundColor: pointBackgroundColor,
            backgroundColor: this.currentColor
          }
        ]
      },
      options
    )
  },
  watch: {
    chartData(newData) {
        this.chartData = newData
        const {
            borderColor,
            pointBorderColor,
            pointBackgroundColor,
            options
            } = this.chartColors;

        this.renderChart(
        {
            labels: this.chartLabels,
            datasets: [
            {
                label: this.label,
                data: this.chartData,
                borderColor: this.currentColor,
                pointBorderColor: pointBorderColor,
                pointBackgroundColor: pointBackgroundColor,
                backgroundColor: this.currentColor
            }
            ]
          },
          options
      )
    }
  }
};
</script>
