<script>
import { Bar } from "vue-chartjs";

export default {
  extends: Bar,
  props: {
    label: {
      type: String
    },
    label2: {
      type: String
    },
    chartData: {
      type: Array 
    },
    chartData2: {
      type: Array 
    },
    chartLabels: {
      type: Array
    },
    options: {
      type: Object
    },
    chartColors: {
      type: Object
    }
  },
  mounted() {
    this.gradient = this.$refs.canvas.getContext('2d').createLinearGradient(0, 0, 0, 450)
    this.gradient.addColorStop(0, 'rgba(255, 0, 0, 0.9)')
    // this.gradient.addColorStop(0.5, 'rgba(0, 0, 255, 0.25)');
    this.gradient.addColorStop(1, 'rgba(0, 0, 255, 0.7)');
    const {
      borderColor,
      pointBorderColor,
      pointBackgroundColor,
      options
    } = this.chartColors;

    this.renderChart(
      {
        labels: this.chartLabels,
        datasets: [
          {
            label: this.label,
            data: this.chartData,
            borderColor: borderColor,
            pointBorderColor: pointBorderColor,
            pointBackgroundColor: pointBackgroundColor,
            backgroundColor: '#ffe66d'
          },
          {
            label: this.label2,
            data: this.chartData2,
            borderColor: borderColor,
            pointBorderColor: pointBorderColor,
            pointBackgroundColor: pointBackgroundColor,
            backgroundColor: '#ff6b6b'
          }
        ]
      },
      options
    )
  },
  watch: {
    chartData(newData) {
        this.chartData = newData
        const {
            borderColor,
            pointBorderColor,
            pointBackgroundColor,
            options
            } = this.chartColors;

        this.renderChart(
        {
            labels: this.chartLabels,
            datasets: [
            {
                label: this.label,
                data: this.chartData,
                borderColor: borderColor,
                pointBorderColor: pointBorderColor,
                pointBackgroundColor: pointBackgroundColor,
                backgroundColor: '#ffe66d'
            },
            {
                label: this.label2,
                data: this.chartData2,
                borderColor: borderColor,
                pointBorderColor: pointBorderColor,
                pointBackgroundColor: pointBackgroundColor,
                backgroundColor: '#ff6b6b'
            }
            ]
          },
          options
      )
    }
  }
};
</script>
