<template>
  <div class="container-fluid" style="width: 60vw;" >
      <div class="d-flex">
        <v-card style="width: 0px" to="/main/social-eco-dev"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="20px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg></v-card>
        <h3 class="mx-auto">Энергетика и жилищно-коммунальное хозяйство</h3>
      </div>
    <div class="row d-flex">
            <div class="dropdown col-5" v-if="MQIndicator">
                <p>Показатель</p>
                <button style="font-size: .7rem; max-width: 20vw; overflow-x: hidden" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlIndex }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow-x: hidden;">
                    <a style="font-size: .7rem" @click="changeIndicator(i)" v-for="i in Object.keys(indicators)" :title="i" :key="i"  class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown col-5" v-if="yearsIndicator">
                <p>Показатель</p>
                <button style="font-size: .7rem; max-width: 20vw; overflow-x: hidden" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlIndex }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow-x: hidden;">
                    <a style="font-size: .7rem" @click="changeIndicatorToYears(i)" v-for="i in Object.keys(indicators)" :title="i" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <button class="btn btn-primary col-2 offset-2" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeMonthes()">Ежемесячные</button>
            <button class="btn btn-primary col-1" style="font-size: .7rem; margin-top: 40px; margin-right: 20px" @click="changeYears()"
            >Годовые</button>
            <div class="dropdown col-1" v-if="yearsBtn1">
                <p>Год</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="changeYearByMonthes(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown col-1" v-if="yearsBtn2">
                <p>Год</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="changeYearByQuarters(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
    </div>
        <div class="row">
            <div class="col-12" v-if="MQChart">
                <HorizontalBar
                :height="200"
                :chartData="changeData"
                :chartLabels="months"
                :chartColors="regionsChartColors"
                :label="htmlIndex"
                />
            </div>
            <div class="col-12" v-if="yearsChart">
                <HorizontalBar
                :height="200"
                :chartData="changeData"
                :chartLabels="byYearsFromYears"
                :chartColors="regionsChartColors"
                :label="htmlIndex"
                />
            </div>
        </div>
  </div>
</template>

<script>
import axios from "axios";

import HorizontalBar from "./HorizontalBar";

export default {
  components: {
    HorizontalBar
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: "rgb(98, 153, 235)",
        pointHitRadius: 10
      },
      htmlYears: '2017',
      htmlIndex: 'Общая протяженность тепловых сетей, из них (Отремонтировано) км',
      regions: [],
      years: [],
      allData: [],
      changeData: [],
      indicators: {},
      indicatorsByMonthes: {'Строительство магистральных трубопроводов, млрд. тг':'constructionGasmainpipelines', 'Строительство водных сооружений, млрд. тг':'constructionWaterfacilities', 'Строительство распределительных обЪектов для обеспечения электроэнергией, млрд. тг':'constructionDistributionfacilities', 'Аварийные отключения теплоснабжения, единиц':'emergencyHeatsupply', 'Аварийные отключения электроэнергии, единиц':'emergencyPoweroutages', 'Аварийные отключения газоснабжения, единиц':'emergencyGassupply', 'Аварийные отключения на сетях водоснабжения':'emergencySupplynetworks', 'Общая протяженность тепловых сетей, из них (Отремонтировано) км':'totalLengthHeatnetworks', 'Общая протяженность электрических сетей 0,4 кВт, из них (Отремонтировано) км':'totalLengthElectricnetworks', 'Общая протяженность водопроводных и канализационных сетей, из них (Отремонтировано) км':'totalLengthWaterseweragenetworks', 'Отремонтировано к началу отопительноного сезона (отремонтировано), единиц':'renovated', 'Водоснабжение, канализационная система, контроль над сбором и распределенем отходов, млрд. тг':'waterWastedistribution', 'Электроснабжение, подача газа, пара, воздушном кондиционировании, млрд. тг':'powerAirconditioning', 'Отремонтировано к началу отопительного сезона (Всего котельных), единиц':'totalBoilerrooms', 'Подготовлено к зимнему отопительному сезону объектов (Образования), единиц':'educations', 'Подготовлено к зимнему отопительному сезону обЪектов (Здравоохранение), единиц':'healths', 'Подготовлено к зимнему отопительному сезону обЪектов (Жилых домов), единиц':'residentialBuilding', 'Потребность в топливе на отопительный период (Уголь), тыс. тонн':'coal', 'Потребность в топливе на отопительный период (Жидкое топливо), тыс. тонн':'liquidFuel'},

      indicatorsByYears: {'Доля обеспеченности доступа в городах к: центральному водоснабжению, %':'centralWatersupply', 'Доля обеспеченности доступа в городах к: центральному водоотведению, %':'centralWaterdisposal', 'Доля обеспеченности доступа в городах к: центральному газоснабжению, %':'centraiGas', 'Доля обеспеченности доступа в сельских населенных пунктах к: Центральному водоснабжению, %':'centralWatersupplya', 'Доля обеспеченности доступа в сельских населенных пунктах к: Центральному водоотведению, %':'centralWaterdisposala', 'Доля обеспеченности доступа в сельских населенных пунктах к: Центральному газоснабжению, %':'centraiGasa', 'Объем производства тепловой энергии, тыс. Гкал':'amountHeatenergy', 'Объем потребления газа, куб. метров':'volumeGasconsumption', 'Производство электроэнергии, тыс. квт. час':'electricityProduction', 'Объем потребления воды, млн. м3':'volumeWaterconsumption', 'Объем потребления питьевой воды, млн. м3':'consumptionDrinkingwater', 'Количество населенных пунктов, единиц':'numberSettlements', 'Количество населенных пунктов (Газифицированных НП (населенных пунктов)), единиц':'numberSettlementsGasified', 'Количество МЖД (Требующие капитального ремонта), единиц':'requiringMajorrepairs', 'Количество МЖД (Отремонтированные), единиц':'renovated', 'Количество МЖД (Аварийные), единиц':'emergency', 'Количество МЖД (Ветхие), единиц':'decrepit', 'Количество МЖД (Снесенные), единиц':'demolished', 'Количество форм управления объектами кондоминимума (ТОО), единиц':'too', 'Количество форм управления объектами кондоминимума (КСК), единиц':'kck', 'Количество форм управления объектами кондоминимума (ИП), единиц':'ip', 'Количество введенных объектов (Электроснабжения), км':'electrosupplies', 'Количество введенных объектов (Водоснабжения), км':'watersupply', 'Количество введенных объектов (Газоснабжения), км':'gassupply', 'Общая потряженность тепловых сетей, км':'totalLengthHeatnetworks', 'Общая протяженность электрических сетей 0,4 кВт':'totalLengthElectricnetworks', 'Общая протяженность водопроводных и канализационных сетей, км':'totalLengthWaterseweragenetworks'},
      yearsBtn1: true,
      yearsBtn2: false,
      yearsIndicator: false,
      MQIndicator: true,
      MQChart: true,
      yearsChart: false,
      byYearsFromYears: [],
      months: []
    };
  },
  methods: {
    changeMonthes() {
        this.htmlIndex = 'Общая протяженность тепловых сетей, из них (Отремонтировано) км'
        this.yearsBtn1 = true
        this.yearsBtn2 = false
        this.MQIndicator = true
        this.MQChart = true
        this.yearsIndicator = false
        this.yearsChart = false

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/energetika/years')
        .then(arrYears => {
            this.years = arrYears.data.sort()
            this.htmlYears = this.years[this.years.length - 1]
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/energetika', {
            params: {
                year: this.htmlYears,
                byMonthes: true
            }
        })
        .then(arrMonths => {
            this.indicators = this.indicatorsByMonthes 
            this.allData = arrMonths.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData.map(d=>d[key])
        })
        .catch(error => console.log(error))
    },
    changeYears() {
        this.htmlIndex = 'Доля обеспеченности доступа в городах к: центральному водоснабжению, %'
        this.yearsBtn1 = false
        this.yearsBtn2 = false
        this.MQIndicator = false
        this.MQChart = false
        this.yearsIndicator = true
        this.yearsChart = true
        this.indicators = this.indicatorsByYears

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/energetika', {
            params: {
                byYears: true
            }
        })
        .then(arrMonths => {
            this.allData = arrMonths.data
            this.byYearsFromYears = []
            arrMonths.data.map(n => this.byYearsFromYears.push(n.year))
            let key = this.indicators[this.htmlIndex]
            this.changeData = arrMonths.data.map(d=>d[key])
            // this.updateStatistics(arrMonths, this.indicators)
        })
        .catch(error => console.log(error))
    },
    changeIndicator(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData.map(d=>d[key])
    },
    changeIndicatorToYears(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData.map(d=>d[key])
    },
    changeYearByMonthes(year) {
        this.htmlYears = year
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/energetika', {
            params: {
                year: year,
                byMonthes: true
            }
        }).then(d => {
            this.indicators = this.indicatorsByMonthes
            this.allData = d.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = this.allData.map(e=>e[key])
        }).catch(err => console.log(err));
    },
  },
  created() {
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/energetika/years')
        .then(arrYears => {
            this.years = arrYears.data.sort()
            this.htmlYears = this.years[this.years.length - 1]
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/energetika', {
            params: {
                year: this.htmlYears,
                byMonthes: true
            }
        })
        .then(arrMonths => {
            this.months = arrMonths.data.map(e => e.month)
            this.indicators = this.indicatorsByMonthes
            this.allData = arrMonths.data
            let key = this.indicators[this.htmlIndex]
            this.changeData = arrMonths.data.map(d=>d[key])
        })
        .catch(error => console.log(error))
  }
};
</script>

<style scoped>
    .nav-link{
        font-size: .7rem;
    }
</style>