<template>
  <div class="container-fluid" v-if="billionBar">
        <h5 class="text-center">Валовый региональный продукт, млрд. тенге</h5>
            <div class="d-flex">
                <div class="dropdown col-2">
                    <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ scenarioHtml }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                        <a style="font-size: .7rem" @click="viewScenario(i)" v-for="i in scenario" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
            </div>
        <div class="row">
            <div class="col-12">
            <p style="text-align: center; font-size: .8rem"><b>Динамика валового регионального продукта, млрд. тенге</b></p>
                <mixedChart
                :height="50"
                :chartData="billionBar"
                :chartData2="billionLine"
                :chartLabels="years"
                :chartColors="regionsChartColors"
                label="ВРП, млрд. тг"
                />
            </div>
        </div>
        <div class="row">
            <div class="col-12">
                <div class="dropdown col-1">
                    <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{ htmlYears }}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="max-height: 200px; overflow: hidden scroll">
                        <a style="font-size: .7rem;" @click="viewYear(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                    </div>
                </div>
                <div class="col-12">
                <p style="text-align: center; font-size: .8rem"><b>ИФО и дефлятор по отраслям, % к предыдущему году</b></p>
                    <BarChart
                    :height="50"
                    :chartData="ifo"
                    :chartData2="deflator"
                    :chartLabels="['ВРП', 'Производство товаров', 'Сельское, лесн, рыбн. хо-тво', 'Промышленность', 'Горнодобывающая п-сть', 'Обрабатывающая промышленность', 'Электроснабжение', 'Водоснабжение', 'Строительство', 'Производство услуг', 'Оптовая и розничная тор-ля', 'Розничная торговля', 'Оптовая торговля', 'Транспорт и складирование', 'Информация и связь', 'Операции с недвижимостью', 'Прочие услуги', 'Валовая добавленная стоимость', 'Чистые налоги']"
                    :chartColors="regionsChartColors"
                    :options="barOptions"
                    label="ИФО"
                    label2="Дефлятор"
                    />
                </div>
            </div>
        </div>
            <p style="text-align: center; font-size: .8rem"><b>ВРП по отраслям, млрд. тенге</b></p>
        <div class="col-12 d-flex justify-center">
            <div class="col-6" v-if="pieGoods" @click="pieFIlter()">
                <p style="font-size: .8rem">Производство товаров</p>
                <PieChart
                :height="150"
                :chartData="goodsPie"
                :chartLabels="['Сельское, лесное и рыбное хозяйство', 'Промышленность', 'Строительство']"
                :chartColors="regionsChartColors"
                />
            </div>
            <div class="col-6" v-if="pieIndustries">
                <p style="font-size: .8rem">Промышленность</p>
                <svg @click="backBtn()" class="link" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="30px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg>
                <PieChart
                :height="150"
                :chartData="industryPie"
                :chartLabels="['Горнодобывающая промышленность', 'Обрабатывающая промышленность', 'Электроснабжение', 'Водоснабжение']"
                :chartColors="regionsChartColors"
                />
            </div>
            <div class="col-6" v-if="pieServices">
                <p style="font-size: .8rem">Производство услуг</p>
                <PieChart
                :height="150"
                :chartData="servicesPie"
                :chartLabels="['Розничная торговля', 'Оптовая торговля', 'Транспорт и складирование', 'Информация и связь', 'Операции с недвижимым имуществом', 'Прочие услуги']"
                :chartColors="regionsChartColors"
                />
            </div>
        </div>
        <ul class="pagination pagination-sm d-flex justify-center mt-2">
            <v-card class="page-link disabled" to="1">1</v-card>
            <v-card class="page-link" to="2">2</v-card>
            <v-card class="page-link" to="3">3</v-card>
            <v-card class="page-link" to="4">4</v-card>
            <v-card class="page-link" to="5">5</v-card>
            <v-card class="page-link" to="6">6</v-card>
            <v-card class="page-link" to="7">7</v-card>
            <v-card class="page-link" to="8">8</v-card>
            <v-card class="page-link" to="9">9</v-card>
        </ul>
  </div>
</template>

<script>
import axios from "axios";

import BarChart from "./BarChart";

import mixedChart from "./mixedChart";

import PieChart from "./PieChart";

export default {
  components: {
    BarChart,
    mixedChart,
    PieChart
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: '#ffe66d',
        pointHitRadius: 10,
        options: {
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: false,
                            
                        }
                    }]
                }
            }
      },
      barOptions: {
            scales: {
                yAxes: [{
                    ticks: {
                        beginAtZero: true,
                    }
                }]
            }
        },
      htmlYears: '2019',
      years: [],
      scenarioHtml: 'Базовый',
      scenario: ['Базовый', 'Оптимистичный', 'Пессимистичный'],
      billionBar: [],
      billionLine: [],
      industry: [],
      indicator: [],
      ifo: [],
      deflator: [],
      goodsPie: [],
      industryPie: [],
      servicesPie: [],
      pieGoods: true,
      pieServices: true,
      pieIndustries: false
    };
  },
  methods: {
    viewYear(year) {
        this.htmlYears = year
        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblastyear', {
            params: {
                year: this.htmlYears
            }
        }).then(arrRegions => {
            this.ifo = []
            this.deflator = []
            this.goodsPie = []
            this.industryPie = []
            this.servicesPie = []

            arrRegions.data.map(d => {
            if (d["indicator"] == 'ИФО ВРП, %' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет') {
                this.ifo.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'Дефлятор ВРП, %' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет') {
                this.deflator.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' && d["industry"] == 'Промышленность') {
                this.goodsPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' && d["industry"] == 'Строительство' && d["investments"] == 'Нет') {
                this.goodsPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' && d["industry"] == 'Сельское, лесное и рыбное хозяйство' && d["investments"] == 'Нет') {
                this.goodsPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Горнодобывающая промышленность и разработка карьеров') {
                this.industryPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' && d["industry"] == 'Обрабатывающая промышленность') {
                this.industryPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Электроснабжение, подача газа, пара и воздушное кондиционирование') {
                this.industryPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Водоснабжение; канализационная система, контроль над сбором и распределением отходов') {
                this.industryPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Прочие услуги') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Оптовая торговля' && d["investments"] == 'Нет') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Розничная торговля') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Транспорт и складирование') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Операции с недвижимым имуществом') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Информация и связь') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
        })
        }).catch(err => console.log(err));
    },
    viewScenario(scenario) {
        this.scenarioHtml = scenario
        this.billionBar = []
        this.billionLine = []

        axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
            params: {
                scenario: this.scenarioHtml
            }
        }).then(arrRegions => {
            arrRegions.data.map(d => {
            if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП') {
                this.billionBar.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ИФО ВРП, %' && d["industry"] == 'ВРП') {
                this.billionLine.push(d['value'].toFixed(1))
            }
            })
        }).catch(err => console.log(err));
    },
    onlyUnique(value, index, self) { 
        return self.indexOf(value) === index;
    },
    pieFIlter() {
        this.pieGoods = false
        this.pieServices = true
        this.pieIndustries = true
    },
    backBtn() {
        this.pieGoods = true
        this.pieServices = true
        this.pieIndustries = false
    }
  },
  created() {
    axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
        params: {
            scenario: 'Базовый'
        }
    })
    .then((arrRegions) => {
        arrRegions.data.map(d => {
            if (d["indicator"] == 'ВРП, млрд. тенге' && d["industry"] == 'ВРП') {
                this.years.push(d['year'])
                this.billionBar.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ИФО ВРП, %' && d["industry"] == 'ВРП') {
                this.billionLine.push(d['value'].toFixed(1))
            }
        })
        // console.log(this.billionBar)
    })
    .catch(error => console.log(error))

    axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblastyear', {
        params: {
            year: this.htmlYears
        }
    })
    .then((arrRegions) => {
        arrRegions.data.map(d => {
            if (d["indicator"] == 'ИФО ВРП, %' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет') {
                this.ifo.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'Дефлятор ВРП, %' && d["scenario"] == 'Базовый' && d["investments"] == 'Нет') {
                this.deflator.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' && d["industry"] == 'Промышленность') {
                this.goodsPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' && d["industry"] == 'Строительство' && d["investments"] == 'Нет') {
                this.goodsPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' && d["industry"] == 'Сельское, лесное и рыбное хозяйство' && d["investments"] == 'Нет') {
                this.goodsPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Горнодобывающая промышленность и разработка карьеров') {
                this.industryPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' && d["industry"] == 'Обрабатывающая промышленность') {
                this.industryPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Электроснабжение, подача газа, пара и воздушное кондиционирование') {
                this.industryPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Водоснабжение; канализационная система, контроль над сбором и распределением отходов') {
                this.industryPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Прочие услуги') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Оптовая торговля' && d["investments"] == 'Нет') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Розничная торговля') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Транспорт и складирование') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Операции с недвижимым имуществом') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
            else if (d["indicator"] == 'ВРП, млрд. тенге' && d["scenario"] == 'Базовый' 
            && d["industry"] == 'Информация и связь') {
                this.servicesPie.push(d['value'].toFixed(1))
            }
        })
        // console.log(this.servicesPie)
    })
    .catch(error => console.log(error))
  }
};
</script>

<style scoped>
    body{
        font-size: .7rem;
    }
    table {
        font-family: arial, sans-serif;
        border-collapse: collapse;
        width: 100%;
        font-size: .8rem;
    }

    td, th {
        border: 1px solid #ffd000;
        text-align: left;
        padding: 8px;
    }

    th {
        background-color: #ffd000;
    }

    tr:nth-child(even) {
        background-color: #dddddd;
    }

    .page-link {
        color: blue
    }

    .disabled {
        color: black
    }

    .link {
        cursor: pointer;
    }
    .link:hover {
        background-color: rgba(20, 20, 255, 0.178);
    }
</style>