<template>
  <div class="container-fluid" style="width: 60vw;" v-if="allData">
      <div class="d-flex">
        <v-card style="width: 0px" to="/main/social-eco-dev"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="black" width="20px" height="18px"><path d="M0 0h24v24H0z" fill="none"/><path d="M21 11H6.83l3.58-3.59L9 6l-6 6 6 6 1.41-1.41L6.83 13H21z"/></svg></v-card>
        <h3 class="mx-auto">Внутренняя политика</h3>
      </div>
    <div class="row d-flex">
            <div class="dropdown col-7">
                <p>Показатель</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuIndex" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlIndex }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuIndex" style="max-height: 200px; overflow-x: hidden;">
                    <a style="font-size: .7rem" @click="changeIndicator(i)" v-for="i in Object.keys(indicators)" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
            <div class="dropdown col-2 offset-3">
                <p>Год</p>
                <button style="font-size: .7rem" class="btn btn-primary text-white dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {{ htmlYears }}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a style="font-size: .7rem" @click="changeYear(i)" v-for="i in years" :key="i" class="dropdown-item">{{ i }}</a>
                </div>
            </div>
    </div>
        <div class="row">
            <div class="col-12">
                <HorizontalBar
                :height="200"
                :chartData="Object.values(changeData)"
                :chartLabels="Object.keys(changeData)"
                :chartColors="regionsChartColors"
                :label="htmlIndex"
                :options="chartOptions"
                />
            </div>
        </div>
  </div>
</template>

<script>
import axios from "axios";

import HorizontalBar from "./HorizontalBar";

export default {
  components: {
    HorizontalBar
  },
  data() {
    return {
      regionsChartColors: {
        borderColor: "#077187",
        pointBorderColor: "#0E1428",
        pointBackgroundColor: "#AFD6AC",
        backgroundColor: "rgb(98, 153, 235)",
        pointHitRadius: 10
      },
      chartOptions: {
        scales: {
            yAxes: [{
                ticks: {
                    beginAtZero: true
                }
            }]
        }
      },
      htmlYears: '2020',
      htmlIndex: 'Тональность материалов, направленных на освещение хода реализации Послания Президента (Положительная тональность), единиц',
      regions: [],
      years: [2014, 2015, 2016, 2017, 2018, 2019, 2020],
      allData: [],
      obj1: [],
      obj2: [],
      changeData: [],
      indicators: {},
      indicators1: {'Уровень доверия населения институтам власти (акиму области), %':'levelPublicConfidenceAkimobl', 'Уровень поддержки населением курса гос. политики (Президенту, Правительству), %':'levelPublicSupport', 'Уровень осведомленности населения об основных направлениях Послания Президента и ходе их реализации, %':'levelAwarenessPopulation', 'Количество поданных заявок на проведение митингов, единиц':'numberApplicationsRallies', 'Карта очагов социальной напряженности по Атырауской области, единиц':'mapHotbedsSocialTension'},
      indicators2: {'Тональность материалов, направленных на освещение хода реализации Послания Президента (Положительная тональность), единиц':'positiveTone', 'Тональность материалов, направленных на освещение хода реализации Послания Президента (Отрицательная тональность), единиц':'negativeTone', 'Тональность материалов, направленных на освещение деятельности акима области (Положительная тональность), единиц':'positiveToneA', 'Тональность материалов, направленных на освещение деятельности акима области (Отрицательная тональность), единиц':'negativeToneA', 'Количество положительных газетных материалов, опубликованных в СМИ, единиц':'numberPositiveNewspaperArticles'},
      monthes: []
    };
  },
  methods: {
    changeIndicator(i) {
        this.htmlIndex = i
        let key = this.indicators[this.htmlIndex]
        this.changeData = this.allData[key]
    },
    changeYear(year) {
        this.htmlYears = year
        this.obj1 = []
        this.obj2 = []

        if(year == '2018' || year == '2019' || year == '2020') {
            axios.get('http://localhost:8070/api/statistics/monitoring-sed/internalq', {
            params: {
                year: year,
                byQuarters: true
            }
            })
            .then(arrMonths => {
                this.obj1.push(arrMonths.data)
                this.obj1 = this.obj1[0]
            })
            .catch(error => console.log(error))

            axios.get('http://localhost:8070/api/statistics/monitoring-sed/intqhy', {
                params: {
                    year: year,
                    byQuarters: true
                }
            })
            .then(arrMonths => {
                this.obj2.push(arrMonths.data)
                this.obj2 = this.obj2[0]
                this.allData = []
                this.changeData = []
                this.allData = Object.assign(this.obj1, this.obj2)
                this.indicators = Object.assign(this.indicators1, this.indicators2)
                let key = this.indicators[this.htmlIndex]
                this.changeData = this.allData[key]
            })
            .catch(error => console.log(error))
        }else {
            this.htmlIndex = 'Тональность материалов, направленных на освещение хода реализации Послания Президента (Положительная тональность), единиц'
            axios.get('http://localhost:8070/api/statistics/monitoring-sed/internalq', {
            params: {
                year: year,
                byQuarters: true
            }
            })
            .then(arrMonths => {
                this.obj1.push(arrMonths.data)
                this.obj1 = this.obj1[0]
                this.allData = []
                this.changeData = []
                this.allData = this.obj1
                this.indicators = this.indicators2
                let key = this.indicators[this.htmlIndex]
                this.changeData = this.allData[key]
            })
            .catch(error => console.log(error))
        }
    }
  },
  created() {
        axios.get('http://localhost:8070/api/statistics/monitoring-sed/internalq', {
            params: {
                year: this.htmlYears,
                byQuarters: true
            }
        })
        .then(arrMonths => {
            this.obj1.push(arrMonths.data)
            this.obj1 = this.obj1[0]
            this.indicators = Object.assign(this.indicators1, this.indicators2)
        })
        .catch(error => console.log(error))

        axios.get('http://localhost:8070/api/statistics/monitoring-sed/intqhy', {
            params: {
                year: this.htmlYears,
                byQuarters: true
            }
        })
        .then(arrMonths => {
            this.obj2.push(arrMonths.data)
            this.obj2 = this.obj2[0]
            let key = this.indicators[this.htmlIndex]
            this.allData = Object.assign(this.obj1, this.obj2)
            this.changeData = this.allData[key]
            // console.log(this.allData)
        })
        .catch(error => console.log(error))
    }
};
</script>