<script>
import { Bar } from "vue-chartjs";

export default {
  extends: Bar,
  props: {
    label: {
      type: String
    },
    label2: {
      type: String
    },
    chartData: {
    },
    chartData2: {
    },
    chartLabels: {
      type: Array
    },
    options: {
      type: Object
    },
    chartColors: {
      type: Object
    }
  },
  mounted() {
    // Chart.defaults.global.legend.align = 'start'

    const {
      borderColor,
      pointBorderColor,
      pointBackgroundColor,
      backgroundColor,
      options
    } = this.chartColors;

    this.renderChart(
      {
        labels: this.chartLabels,
        datasets: [
          {
            type: 'bar',
            label: this.label,
            data: this.chartData,
            borderColor: borderColor,
            pointBorderColor: pointBorderColor,
            pointBackgroundColor: pointBackgroundColor,
            backgroundColor: backgroundColor
          },
          {
              type: 'line',
              label: this.label2,
              data: this.chartData2,
              borderColor: '#f07167',
              backgroundColor: "rgba(31, 31, 255, 0)"
          },
        ]
      },
      options
    )
  },
   watch: {
    chartData(newData) {
        this.chartData = newData
        const {
            borderColor,
            pointBorderColor,
            pointBackgroundColor,
            backgroundColor,
            options
            } = this.chartColors;

        this.renderChart(
        {
            labels: this.chartLabels,
            datasets: [
            {
                type: 'bar',
                label: this.label,
                data: this.chartData,
                borderColor: borderColor,
                pointBorderColor: pointBorderColor,
                pointBackgroundColor: pointBackgroundColor,
                backgroundColor: backgroundColor
            },
            {
                type: 'line',
                label: 'ИФО, % к предыдущему году',
                data: this.chartData2,
                borderColor: '#f07167',
                backgroundColor: "rgba(31, 31, 255, 0)"
            }
            ]
        },
          options
      )
    }
  }
};
</script>
